import {
   ChangeDetectionStrategy,
   Component, EventEmitter,
   Input, Output,
} from "@angular/core";
import {Status} from "@component/card/status";
import {VsanUiUtils} from "@util/vsan-util";

/**
 * An expandable card component with a status label in the header. It provides:
 *  - expand/collapse of the content using vsan-card.
 *  - header with a status label. For example a service with status: Enabled/Disabled.
 *  - any footer with actions is projected.
 *  - Change detection OnPush, so no service calls should be made inside this component.
 */
@Component({
   selector: "vsan-status-card",
   templateUrl: "status-card.html",
   styleUrls: [ "status-card.scss"],
   changeDetection: ChangeDetectionStrategy.OnPush,
})
export class StatusCardComponent {

   private static readonly LABEL_CLASS: string = "label";
   private static readonly LABEL_SUCCESS_CLASS: string = "label-success";
   private static readonly LABEL_PARTIAL_CLASS: string = "label-warning";

   private static readonly ENABLED_SERVICE_LABELS: string[] = [
         StatusCardComponent.LABEL_CLASS,
         StatusCardComponent.LABEL_SUCCESS_CLASS,
   ];

   private static readonly DISABLED_SERVICE_LABELS: string[] = [
      StatusCardComponent.LABEL_CLASS,
   ];

   private static readonly PARTIAL_ENABLED_SERVICE_LABELS: string[] = [
         StatusCardComponent.LABEL_CLASS,
         StatusCardComponent.LABEL_PARTIAL_CLASS,
   ];

   /*
    * The service name. Should be localized.
    */
   @Input()
   label: string;

   /*
    * No status means no label will be shown next to the service name.
    * Otherwise we show a label with "Enabled"/"Disabled", depending on the passed value.
    */
   @Input()
   status: Status;

   /**
    * If set to true, the card header will have cdkDragHeader attribute added, which
    * shows a "move" cursor. This can be used in the context of drag lists.
    */
   @Input()
   allowDragging: boolean;

   private _isExpanded: boolean;

   /*
    * Whether to expand or collapse the service card.
    */
   @Input()
   set isExpanded(value: boolean) {
      if (this._isExpanded !== value) {
         this._isExpanded = value;
         this.isExpandedChange.emit(this._isExpanded);
      }
   }

   get isExpanded(): boolean {
      // Always coerce as non-set value /i.e., undefined/ is considered true.
      // See https://bugzilla.eng.vmware.com/show_bug.cgi?id=2836825
      return !!this._isExpanded;
   }

   @Output()
   isExpandedChange: EventEmitter<boolean> = new EventEmitter<boolean>();

   get statusLabelClass(): string {
      if (this.status === Status.Enabled) {
         return StatusCardComponent.ENABLED_SERVICE_LABELS.join(" ");
      } else if (this.status === Status.Partial) {
         return StatusCardComponent.PARTIAL_ENABLED_SERVICE_LABELS.join(" ");
      }
      return StatusCardComponent.DISABLED_SERVICE_LABELS.join(" ");
   }

   get statusLabel(): string {
      if (this.status === Status.Enabled) {
         return VsanUiUtils.getString("vsan.services.enabled");
      } else if (this.status === Status.Partial) {
         return VsanUiUtils.getString("vsan.services.partial");
      }
      return VsanUiUtils.getString("vsan.services.disabled");
   }
}
