import {Component, OnInit} from "@angular/core";
import {FormBuilder, FormControl, FormGroup, Validators} from "@angular/forms";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {ModalBuilder} from "@util/modal-builder";
import {AdvancedOptionsService} from "@generated/advanced-options-service";
import {AdvancedOptionsInfo} from "@generated/advanced-options-info";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {VsanStretchedClusterService} from "@generated/vsan-stretched-cluster-service";
import {VsanRdmaService} from "@generated/vsan-rdma-service";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {SrMessageType} from "@service/screen-reader-announcer.service";
import {ErrorHandler} from "@service/flow/error-handler.decorator";
import {LoaderName} from "@service/flow/loader-name";

@Component({
   templateUrl: "edit-advanced-options.html",
   styleUrls: ["edit-advanced-options.scss"],
   selector: "vsan-edit-advanced-options",
})
export class EditAdvancedOptionsComponent implements OnInit {

   static readonly MIN_OBJECT_REPAIR_TIMER: number = 0;
   static readonly MAX_OBJECT_REPAIR_TIMER: number = 4294967295;
   static readonly MIN_REBALANCING_THRESHOLD: number = 20;
   static readonly MAX_REBALANCING_THRESHOLD: number = 75;

   private static readonly DIALOG_HEIGHT: number = 520;

   private advancedOptionsInfo: AdvancedOptionsInfo;

   private isFormInvalid: boolean;

   @LoadingIndicator({
      name: [
         LoaderName.ON_INIT, LoaderName.PERFORM_CHANGE],
   })
   loading: boolean;

   alertMessages: string[] = [];

   clusterRef: ManagedObject;
   clusterName: string;

   isStretchedCluster: boolean;
   isRdmaEnabled: boolean;

   clusterCapabilityData: VsanCapabilityData;
   vcCapabilityData: VsanCapabilityData;

   advancedSettings: FormGroup;
   // Constants to use for the form fields
   readonly objectRepairTimer: string = "objectRepairTimer";
   readonly isSiteReadLocalityEnabled: string = "isSiteReadLocalityEnabled";
   readonly isCustomizedSwapObjectEnabled: string = "isCustomizedSwapObjectEnabled";
   readonly largeClusterSupportEnabled: string = "largeClusterSupportEnabled";
   readonly isGuestTrimUnmapEnabled: string = "isGuestTrimUnmapEnabled";
   readonly isAutomaticRebalanceEnabled: string = "isAutomaticRebalanceEnabled";
   readonly rebalancingThreshold: string = "rebalancingThreshold";

   constructor(private advancedOptionsService: AdvancedOptionsService,
         private stretchedClusterService: VsanStretchedClusterService,
         private rdmaService: VsanRdmaService,
         private formBuilder: FormBuilder) {
      const formSettings: any = {};
      formSettings[this.objectRepairTimer] = new FormControl("", [
         Validators.required,
         Validators.min(EditAdvancedOptionsComponent.MIN_OBJECT_REPAIR_TIMER),
         Validators.max(EditAdvancedOptionsComponent.MAX_OBJECT_REPAIR_TIMER),
      ]);
      formSettings[this.isSiteReadLocalityEnabled] = "";
      formSettings[this.isCustomizedSwapObjectEnabled] = "";
      formSettings[this.largeClusterSupportEnabled] = "";
      formSettings[this.isGuestTrimUnmapEnabled] = "";
      this.advancedSettings = this.formBuilder.group(formSettings);
   }

   @Loader({
      name: LoaderName.ON_INIT,
      srMessage: "vsan.services.advancedOptions.title",
      srMessageType: SrMessageType.PAGE_TITLE,
   })
   async ngOnInit(): Promise<void> {
      const context: any = VsanUiUtils.getModalContext();
      this.clusterName = context.clusterName;
      this.clusterRef = context.clusterRef;
      this.vcCapabilityData = context.vcCapabilityData;
      this.clusterCapabilityData = context.clusterCapabilityData;

      if (this.vcCapabilityData && this.vcCapabilityData.isAutomaticRebalanceSupported) {
         this.advancedSettings.addControl(this.isAutomaticRebalanceEnabled, new FormControl(""));
         this.advancedSettings.addControl(this.rebalancingThreshold,
               new FormControl("", [
                  Validators.required,
                  Validators.min(EditAdvancedOptionsComponent.MIN_REBALANCING_THRESHOLD),
                  Validators.max(EditAdvancedOptionsComponent.MAX_REBALANCING_THRESHOLD),
               ]));
         VsanUiUtils.setModalDialogHeight(EditAdvancedOptionsComponent.DIALOG_HEIGHT + 80);
      }

      const results: any[] = await Promise.all([
         this.advancedOptionsService.getAdvancedOptionsInfo(this.clusterRef),
         this.stretchedClusterService.getIsVsanStretchedCluster(this.clusterRef),
         this.rdmaService.isRdmaEnabled(this.clusterRef)]);

      this.handleInfoResult(results[0]);
      this.isStretchedCluster = results[1];
      this.isRdmaEnabled = results[2];


      /*
      * setTimeout prevents modal to get too early formStatus property because of checkbox asynchronous disable
      * */
      this.advancedSettings.statusChanges.subscribe(value => setTimeout(() => {
         this.isFormInvalid = value === "INVALID";
      }));
   }

   private handleInfoResult = (result: AdvancedOptionsInfo) => {
      this.advancedOptionsInfo = result;
      this.advancedSettings.get(this.objectRepairTimer).setValue(result.objectRepairTimer);
      this.advancedSettings.get(this.isSiteReadLocalityEnabled).setValue(result.isSiteReadLocalityEnabled);
      this.advancedSettings.get(this.isCustomizedSwapObjectEnabled).setValue(result.isCustomizedSwapObjectEnabled);
      this.advancedSettings.get(this.largeClusterSupportEnabled).setValue(result.largeClusterSupportEnabled);
      this.advancedSettings.get(this.isGuestTrimUnmapEnabled).setValue(result.isGuestTrimUnmapEnabled);
      this.advancedSettings.get(this.isAutomaticRebalanceEnabled).setValue(result.isAutomaticRebalanceEnabled);
      this.advancedSettings.get(this.rebalancingThreshold).setValue(result.rebalancingThreshold);
   };

   @ErrorHandler({name: LoaderName.ON_INIT})
   private handleRequestError(error: string): void {
      this.alertMessages = [error];
   }

   get blockApplyButton(): boolean {
      return this.loading || this.isFormInvalid;
   }

   get rebalancingThresholdEnabled(): boolean {
      return this.advancedSettings.get(this.isAutomaticRebalanceEnabled).value === true;
   }

   get largeClusterSupportMessage(): string {
      const messages: string[] = [];
      if (this.isRdmaEnabled) {
         messages.push(VsanUiUtils.getString("vsan.services.advancedOptions.largeClusterSupport.signpost.rdmaEnabled"));
      }

      messages.push(VsanUiUtils.getString("vsan.services.advancedOptions.largeClusterSupport.signpost"));

      return messages.join(" ");
   }

   private isConfigDifferent = (): boolean => {
      const options: AdvancedOptionsInfo = this.getConfiguredAdvancedOptionsInfo();
      return this.advancedOptionsInfo.objectRepairTimer !== options.objectRepairTimer ||
            this.advancedOptionsInfo.isSiteReadLocalityEnabled !== options.isSiteReadLocalityEnabled ||
            this.advancedOptionsInfo.isCustomizedSwapObjectEnabled !== options.isCustomizedSwapObjectEnabled ||
            this.advancedOptionsInfo.isGuestTrimUnmapEnabled !== options.isGuestTrimUnmapEnabled ||
            this.advancedOptionsInfo.isAutomaticRebalanceEnabled !== options.isAutomaticRebalanceEnabled ||
            (this.advancedOptionsInfo.isAutomaticRebalanceEnabled &&
                  this.advancedOptionsInfo.rebalancingThreshold !== options.rebalancingThreshold) ||
            this.isLargeScaleClusterSupportDifferent();
   };

   private isLargeScaleClusterSupportDifferent = (): boolean => {
      return this.advancedOptionsInfo.largeClusterSupportEnabled !==
            this.getConfiguredAdvancedOptionsInfo().largeClusterSupportEnabled;
   };

   confirm = () => {
      if (this.isConfigDifferent()) {
         if (this.isLargeScaleClusterSupportDifferent()) {
            new ModalBuilder("services/editAdvancedOptionsConfirmation")
                  .setSize(400, 230)
                  .open(null)
                  .then(result => {
                     this.performChange();
                  });
         } else {
            this.performChange();
         }
      } else {
         this.close();
      }
   };

   @Loader({name: LoaderName.PERFORM_CHANGE})
   private async performChange(): Promise<void> {
      const taskRef: ManagedObject = await this.advancedOptionsService
            .configureAdvancedOptions(this.clusterRef, this.getConfiguredAdvancedOptionsInfo());
      this.close(taskRef);
   }

   @ErrorHandler({name: LoaderName.PERFORM_CHANGE})
   private handlePerformChangeError(): void {
      this.alertMessages = [VsanUiUtils.getString("vsan.services.advancedOptions.edit.error")];
      this.close();
   }

   private close = (taskRef?: ManagedObject) => {
      VsanUiUtils.closeModalDialog(taskRef);
   };

   private getConfiguredAdvancedOptionsInfo = (): AdvancedOptionsInfo => {
      return AdvancedOptionsInfo.Factory.create(
            this.advancedSettings.get(this.objectRepairTimer).value,
            this.advancedSettings.get(this.isSiteReadLocalityEnabled).value,
            this.advancedSettings.get(this.isCustomizedSwapObjectEnabled).value,
            this.advancedSettings.get(this.largeClusterSupportEnabled).value,
            this.advancedSettings.get(this.isGuestTrimUnmapEnabled).value,
            this.advancedSettings.get(this.isAutomaticRebalanceEnabled).value,
            this.advancedSettings.get(this.rebalancingThreshold).value,
            this.advancedOptionsInfo.capacityReservationConfig);
   };
}
