import {Component, OnInit} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {EncryptionRekeySpec} from "@generated/encryption-rekey-spec";
import {EncryptionMutationProvider} from "@generated/encryption-mutation-provider";
import {Privileges} from "@generated/privileges";

@Component({
   templateUrl: "generate-encryption-keys.component.html",
   styleUrls: ["generate-encryption-keys.component.scss"],
   selector: "vsan-generate-encryption-keys",
})
export class GenerateEncryptionKeysComponent implements OnInit {

   reEncryptData: boolean = false;
   allowReducedRedundancy: boolean = false;
   eraseDisksBeforeUse: boolean;
   editClusterPermission: boolean = false;
   manageKeysPermission: boolean = false;

   private clusterRef: ManagedObject;

   constructor(private encryptionProvider: EncryptionMutationProvider) {}

   ngOnInit() {
      this.clusterRef = ManagedObject.contextObject;
      const context: any = VsanUiUtils.getModalContext();
      this.eraseDisksBeforeUse = context != null ? context.eraseDisksBeforeUse : false;
      const permissions: Map<string, boolean> = context.permissions;
      this.editClusterPermission = permissions.get(Privileges.EDIT_CLUSTER());
      this.manageKeysPermission = permissions.get(Privileges.MANAGE_KEYS());
   }

   ok(): void {
      const spec: EncryptionRekeySpec =
            EncryptionRekeySpec.Factory.create(this.reEncryptData, this.allowReducedRedundancy);
      this.encryptionProvider.rekeyEncryptedCluster(this.clusterRef, spec).then(() => {
         VsanUiUtils.closeModalDialog();
      });
   }

   close(): void {
      VsanUiUtils.closeModalDialog();
   }

   get rekeyAllowed(): boolean {
      return this.editClusterPermission && this.manageKeysPermission;
   }

   get validationErrors(): string[] {
      const errors: string[] = [];
      if (this.reEncryptData) {
         errors.push(VsanUiUtils.getString("vsan.encryption.rekey.dialog.reEncrypt.warning"));
         if (this.eraseDisksBeforeUse) {
            errors.push(VsanUiUtils.getString("vsan.encryption.rekey.dialog.reEncrypt.eraseDisksBeforeUse.warning"));
         }
      }
      return errors;
   }
}