import {Component, Input, OnInit} from "@angular/core";
import {CapacityManagementStatus, CapacityReservationUtil} from "@util/capacity-reservation-util";
import {ModalBuilder} from "@util/modal-builder";
import {ManagedObject} from "@service/managed-object";
import {UpgradeData} from "@component/upgrade/upgrade.data";
import {UpgradeService} from "@component/upgrade/upgrade.service";
import {CapacityManagementContext} from "@navigation/context/capacity-management-context";
import {Privileges} from "@generated/privileges";
import {PersistedProperty} from "@service/browser-storage.service";
import {VsanDialogUtil} from "@util/vsan-dialog-util";
import {CapacityManagementData} from "@generated/capacity-management-data";
import {CapacityManagementService} from "@generated/capacity-management-service";
import {TaskMonitorService} from "@service/task-monitor-service";
import {RefreshHandler} from "@service/global-refresh.service";
import {VsanRoutes} from "../../vsan-routes";

@Component({
   templateUrl: "capacity-management-settings.html",
   selector: "vsan-capacity-management-settings",
})
export class CapacityManagementSettingsComponent implements OnInit {
   @Input()
   isSlackSpaceCapacitySupported: boolean;

   @Input()
   isCapacityCustomizableThresholdsSupportedOnVc: boolean;

   @Input()
   permissions: Map<string, boolean>;

   @Input()
   clusterName: string;

   @Input()
   upgradeData: UpgradeData;

   @PersistedProperty("vsan-capacity-reserve-card-status")
   isExpanded: boolean;

   errorMessages: string[] = [];

   capacityManagementData: CapacityManagementData;

   private hasEditPermissions: boolean;

   private isCapacityManagementDataLoaded: boolean;

   constructor(private upgradeService: UpgradeService,
               private capacityManagementService: CapacityManagementService,
               private taskService: TaskMonitorService) {
   }

   ngOnInit(): void {
      this.hasEditPermissions = this.permissions.get(Privileges.EDIT_CLUSTER())
            && this.permissions.get(Privileges.CONFIG_STORAGE());
      this.fetch();
   }

   @RefreshHandler
   fetch(): void {
      this.isCapacityManagementDataLoaded = false;
      this.capacityManagementService.getCapacityManagementData(ManagedObject.contextObject)
          .then(result => this.capacityManagementData = result)
          .catch(e => this.errorMessages.push(e))
          .finally(() => { this.isCapacityManagementDataLoaded = true; });
   }

   // hide loading indicator when both CapacityManagementData and upgradeData are available
   get loading(): boolean {
      return !this.upgradeData || !this.isCapacityManagementDataLoaded;
   }

   get capacityManagementStatus(): CapacityManagementStatus {
      if (this.loading) {
         // The config is still loading
         return new CapacityManagementStatus(false, false);
      }

      const isCapacityReservationSupported: boolean =
            CapacityReservationUtil.isReservationEditingApplicable(
                  this.capacityManagementData.capacityReservationConfig);

      return CapacityReservationUtil.getCapacityManagementStatus(this.hasEditPermissions,
            this.upgradeData, isCapacityReservationSupported, this.isCapacityCustomizableThresholdsSupportedOnVc);
   }

   edit(): void {
      const context: CapacityManagementContext = {
         clusterName: this.clusterName,
         showOldFormatInfo: this.upgradeService.isUpgradeNeeded(this.upgradeData),
         isCapacityCustomizableThresholdsSupported: this.isCapacityCustomizableThresholdsSupportedOnVc,
      };
      new ModalBuilder(VsanRoutes.CAPACITY_MANAGEMENT)
            .setSize(VsanDialogUtil.CAPACITY_MANAGEMENT_DIALOG.width, VsanDialogUtil.CAPACITY_MANAGEMENT_DIALOG.height)
            .open(context)
            .then((taskRef: ManagedObject) => {
               this.taskService.getAwaitResult(taskRef)
                   .then(() => this.fetch())
                   .catch(e => this.errorMessages.push(e));
            });
   }
}
