import {Validator} from "@util/validation/validator";

export class ValidatorsGroup<TEntity> {

   constructor(private validators: Validator<TEntity>[]) { }

   validate(entity: TEntity): boolean {
      return this.validators
            .every(validator => validator.validate(entity));
   }

   getAllErrors(entity: TEntity): string[] {
      return this.validators
            .filter(validator => !validator.validate(entity))
            .map(validator => validator.getErrorMessage(entity));
   }

   getFirstError(entity: TEntity): string {
      return this.validators
            .find(validator => !validator.validate(entity))
            ?.getErrorMessage(entity) || null;
   }
}
