import {Component, OnInit, QueryList, ViewChildren} from "@angular/core";
import {CardName, CardView} from "@component/drag-and-drop/card-view";
import {CardContainer, DragCardPosition} from "@component/drag-and-drop/drag-card-position";
import {DraggableCardDirective} from "@component/drag-and-drop/draggable-card.directive";
import {GeneralConfigData} from "@component/general-settings/general-config.data";
import {VsanGeneralConfigService} from "@component/general-settings/vsan-general-config.service";
import {UnavailableViewSpec} from "@component/unavailable-view/unavailable-view-spec";
import {UpgradeData} from "@component/upgrade/upgrade.data";
import {UpgradeService} from "@component/upgrade/upgrade.service";
import {AlertActionLink} from "@component/validation/alert-action-link";
import {CeipService} from "@generated/ceip-service";
import {ClusterMode} from "@generated/cluster-mode";
import {ClusterPowerState} from "@generated/cluster-power-state";
import {CsdService} from "@generated/csd-service";
import {DomainOrHostData} from "@generated/domain-or-host-data";
import {MountedRemoteDatastore} from "@generated/mounted-remote-datastore";
import {PerfStatsObjectInfo} from "@generated/perf-stats-object-info";
import {Privileges} from "@generated/privileges";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {VsanConfigService} from "@generated/vsan-config-service";
import {VsanConfigSpec} from "@generated/vsan-config-spec";
import {VsanServiceData} from "@generated/vsan-service-data";
import {VsanServiceStatus} from "@generated/vsan-service-status";
import {VsanStretchedClusterService} from "@generated/vsan-stretched-cluster-service";
import {InlineViewContextKey} from "@model/inline-view-context-key.data";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {PersistentStorageService} from "@service/browser-storage.service";
import {ClusterComputeResource} from "@service/client/dataservice/data-service-property";
import {DataServiceClient} from "@service/client/dataservice/data-service.service";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {RefreshHandler} from "@service/global-refresh.service";
import {ManagedObject} from "@service/managed-object";
import {StatusChangeType, VsanStatusChangeData} from "@service/status-updates/vsan-status-change.data";
import {VsanStatusNotificationService} from "@service/status-updates/vsan-status-notification.service";
import {TaskMonitorService, TaskStatus} from "@service/task-monitor-service";
import {ClusterPowerStateUtils} from "@util/cluster-power-state-utils";
import {ModalBuilder} from "@util/modal-builder";
import {VsanUiUtils} from "@util/vsan-util";
import {VsanRoutes} from "../../vsan-routes";
import {QuickstartNotificationService} from "../hci/status/quickstart-notification.service";

@Component({
   templateUrl: "services.html",
   styleUrls: ["services.scss"],
   selector: "vsan-services",
})
export class ServicesComponent implements OnInit {

   ClusterPowerStateUtils: typeof ClusterPowerStateUtils = ClusterPowerStateUtils;

   // Service view cards
   readonly SUPPORT_INSIGHT_CARD: string = "supportInsight";
   readonly PERFORMANCE_SERVICE_CARD: string = "performanceService";
   readonly FILE_SERVICE_CARD: string = "fileService";
   readonly ISCSI_SERVICE_CARD: string = "iscsiTargetService";
   readonly DATA_SERVICES_CARD: string = "dataServices";
   readonly CAPACITY_RESERVE_CARD: string = "capacityReserve";
   readonly ADVANCED_OPTIONS_CARD: string = "advancedOptions";
   readonly NETWORK_OPTIONS_CARD: string = "networkOptions";
   readonly HISTORICAL_HEALTH_CARD: string = "historicalHealth";
   readonly VSAN_MAX_CARD: string = "vsanMax";
   readonly EMPTY_SPACE: string = " ";
   cardsView: CardView;

   // Common config data
   vsanConfig: VsanConfigSpec;
   clusterRef: ManagedObject;
   clusterName: string;
   hostCount: number;
   upgradeData: UpgradeData;
   ceipEnabled: boolean;
   hasMountedRemoteDatastore: boolean;
   clusterPowerState: ClusterPowerState;

   // Permissions
   clusterPermissions: Map<string, boolean>;
   vcPermissions: Map<string, boolean>;
   isHAEnabled: boolean = false;
   isDPMEnabled: boolean = false;
   editClusterPermission: boolean = false;
   globalSettingsPermissions: boolean = false;
   configureStoragePermission: boolean = false;
   clusterPowerPermission: boolean = false;
   unavailableViewActionPermission: boolean = false;

   // Capabilities
   vcCapabilityData: VsanCapabilityData;
   clusterCapabilityData: VsanCapabilityData;

   // Page properties
   unavailableViewSpec: UnavailableViewSpec;
   errorMessages: string[] = [];
   successMessages: string[] = [];
   supportInsightError: string;
   pageTitleFocused: any;

   // The service cards are defined inside templates, so they can be dragged
   @ViewChildren(DraggableCardDirective) serviceTemplates: QueryList<DraggableCardDirective>;

   availableDomains: DomainOrHostData[];
   isStretchedCluster: boolean;
   perfSvsStatus: VsanServiceStatus;
   perfSvcConfig: PerfStatsObjectInfo;
   perfSvcEnabled: boolean;

   showCards: boolean = false;
   isVsanUpgradeVisible: boolean = false;

   @LoadingIndicator()
   isLoading: boolean;

   private isEnableOrDisableVsanTaskFailed: boolean = false;
   isClusterPowerStateChanged: boolean = false;

   constructor(private dataService: DataServiceClient,
         private vsanGeneralConfigService: VsanGeneralConfigService,
         private ceipSvc: CeipService,
         private taskService: TaskMonitorService,
         private quickstartStatusNotification: QuickstartNotificationService,
         private upgradeService: UpgradeService,
         private configService: VsanConfigService,
         private stretchedClusterService: VsanStretchedClusterService,
         private csdService: CsdService,
         private navigationService: NavigationService,
         private storageService: PersistentStorageService,
         private statusNotificationService: VsanStatusNotificationService) {
      this.cardsView = new CardView(CardName.SERVICES_VIEW, this.getServicesViewCards);
      this.statusNotificationService.subscribeForStatusChangeStarted(
            [StatusChangeType.VSAN_CLUSTER_POWER_ON, StatusChangeType.VSAN_CLUSTER_POWER_OFF],
            () => this.refresh());
      // Use this to make the services page in loading state as soon as vSAN is enabled/disabled
      this.statusNotificationService.subscribeForStatusChangeStarted(
            [StatusChangeType.VSAN_SERVICE_ENABLED, StatusChangeType.VSAN_SERVICE_DISABLED],
            () => this.awaitVsanStatusChange(),
      );
      // PR 2786238: Make sure we show the up to date configuration info after vSAN is fully enabled.
      this.statusNotificationService.subscribeForStatusChangeCompleted(
            [StatusChangeType.VSAN_SERVICE_ENABLED, StatusChangeType.VSAN_SERVICE_DISABLED],
            (statusChangeData) => this.handleVsanStatusChangeComplete(statusChangeData));
   }

   ngOnInit(): void {
      this.clusterRef = this.getClusterRefFromLocalStorage() || ManagedObject.contextObject;
      this.refresh();
   }

   @Loader({srMessage: "manage.vsan.general"})
   @RefreshHandler
   async refresh(): Promise<void> {
      this.reset();
      try {
         await Promise.all([
            this.queryVsanGeneralConfig(),
            this.queryDataserviceProperties(),
         ]);
         if (!this.vsanConfig) {
            this.errorMessages = [VsanUiUtils.getString("vsan.services.configuration.not.available")];
            return;
         }

         if (!this.isComputeOnlyCluster()) {
            await Promise.all([
               this.fetchPerfServiceStatus(),
               this.fetchCeipServiceEnabled(),
               this.fetchStretchedClusterData(),
               this.fetchIsStretchedCluster(),
            ]);
         } else {
            const datastores: MountedRemoteDatastore[] = await this.csdService.getMountedDatastores(this.clusterRef);
            this.hasMountedRemoteDatastore = datastores.length > 0;
         }

         if (this.isClusterPowerStateChanged) {
            this.notifyClusterPowerStateChanged();
         }

         this.showCards = !this.isUnavailable();
         if (!this.showCards) {
            this.prepareDisabledSpec();
         } else if (!this.isComputeOnlyCluster()) {
            this.fetchDisksUpgradeData();
         }
         // Focus page title via directive var. Object is passed to trigger model change on page refresh.
         this.pageTitleFocused = {};
      } catch (e) {
         this.errorMessages = [e];
      } finally {
         this.registerQuickstartNotification();
      }
   }

   private isUnavailable(): boolean {
      return ClusterPowerStateUtils.isClusterPoweredOff(this.clusterPowerState)
            || !this.vsanConfig.isEnabled
            || this.isComputeOnlyCluster();
   }

   vsanUpgradeComplete(): void {
      this.isVsanUpgradeVisible = true;
      this.fetchDisksUpgradeData();
   }

   private getClusterRefFromLocalStorage(): ManagedObject {
      return this.storageService.pop(InlineViewContextKey.SERVICES_CONTEXT_KEY);
   }

   /**
    * Reset everything except vsanConfig due to PR 2710611. The refresh method could be excecuted by multiple trigger at
    * the same time. If this happens, and we reset this.vsanConfig to null, the check at line 137 throws exception.
    */
   private reset(): void {
      this.errorMessages = [];
      this.successMessages = [];
      this.unavailableViewSpec = null;
      this.upgradeData = null;
      this.ceipEnabled = true; // Use true to hide the card, while the data is loading.
      this.isDPMEnabled = false;
      this.isHAEnabled = false;
   }

   /**
    * Using @Loader annotation on the "processVsanStatusChange" method makes the page change its loading state constantly
    * as it completes multiple times, before the status gets changed. To fix this issue we need to move the
    * @Loader annotation here, so that only when all promises of "processVsanStatusChange" get resolved,
    * the loading indicator will be set to false.
    */
   @Loader()
   private async awaitVsanStatusChange(): Promise<void> {
      await this.processVsanStatusChange();
   }

   private async processVsanStatusChange(): Promise<void> {
      const originalIsVsanEnabled: boolean = this.vsanConfig.isEnabled;
      const originalIsComputeOnlyCluster: boolean = this.isComputeOnlyCluster();
      // The task ended with an error, so the status will not change, stop waiting and resolve the promise.
      if (this.isEnableOrDisableVsanTaskFailed) {
         this.isEnableOrDisableVsanTaskFailed = false;
         return Promise.resolve();
      }

      try {
         await this.queryVsanGeneralConfig();
         if (originalIsComputeOnlyCluster !== this.isComputeOnlyCluster() // vSAN HCI compute only was enabled or disabled
               || originalIsVsanEnabled !== this.vsanConfig.isEnabled) { // vSAN was enabled/disabled
            VsanUiUtils.globalRefresh();
            return Promise.resolve();
         }
      } catch (e) {
         this.errorMessages.push(e);
         return Promise.resolve();
      }

      return new Promise((resolve) => {
         setTimeout(() => {
            resolve(this.processVsanStatusChange());
         }, 1000);
      });
   }

   async queryVsanGeneralConfig(): Promise<void> {
      try {
         const generalConfig: GeneralConfigData = await this.vsanGeneralConfigService.getVsanGeneralConfigData(
               this.clusterRef);
         this.vsanConfig = generalConfig.cloneVsanConfigSpec();
         this.vcCapabilityData = generalConfig.vcCapabilityData;
         this.clusterCapabilityData = generalConfig.clusterCapabilityData;
         this.clusterPowerState = generalConfig.clusterPowerState;
         this.clusterPermissions = generalConfig.clusterPermissions;
         this.vcPermissions = generalConfig.vcPermissions;
         this.editClusterPermission = this.clusterPermissions.get(Privileges.EDIT_CLUSTER());
         this.configureStoragePermission = this.clusterPermissions.get(Privileges.CONFIG_STORAGE());
         this.clusterPowerPermission = this.clusterPermissions.get(Privileges.SHUTDOWN_CLUSTER());
         this.globalSettingsPermissions = this.vcPermissions.get(Privileges.GLOBAL_SETTINGS());
      } catch (e) {
         this.errorMessages.push(e);
      }
   }

   private async queryDataserviceProperties(): Promise<void> {
      try {
         const dsProps: Map<string, any> = await this.dataService.getProperties([
            ClusterComputeResource.name,
            ClusterComputeResource.host.length,
            ClusterComputeResource.configurationEx.dasConfig.enabled,
            ClusterComputeResource.configurationEx.dpmConfigInfo.enabled,
         ], this.clusterRef);
         this.clusterName = dsProps.get(ClusterComputeResource.name);
         this.hostCount = dsProps.get(ClusterComputeResource.host.length);
         this.isHAEnabled = dsProps.get(ClusterComputeResource.configurationEx.dasConfig.enabled);
         this.isDPMEnabled = dsProps.get(ClusterComputeResource.configurationEx.dpmConfigInfo.enabled);
      } catch (e) {
         this.errorMessages.push(e);
      }
   }

   isComputeOnlyCluster(): boolean {
      return this.vsanConfig?.clusterMode === ClusterMode.COMPUTE;
   }

   private prepareDisabledSpec(): void {
      this.unavailableViewActionPermission = this.editClusterPermission;
      if (ClusterPowerStateUtils.isClusterPoweredOff(this.clusterPowerState)) {
         this.unavailableViewSpec = UnavailableViewSpec.SERVICES_VIEW_CLUSTER_SHUT_DOWN;
         this.unavailableViewActionPermission = this.unavailableViewActionPermission && this.clusterPowerPermission;
      } else if (this.isComputeOnlyCluster()) {
         if (this.isClusterChangingPowerState) {
            // If power-off/on is in progress, do not show the compute only view
            return;
         }
         if (this.hostCount === 0) {
            this.unavailableViewSpec = UnavailableViewSpec.SERVICES_VIEW_COMPUTE_ONLY_CLUSTER_NO_HOSTS;
         } else if (this.hasMountedRemoteDatastore) {
            this.unavailableViewSpec = UnavailableViewSpec.SERVICES_VIEW_COMPUTE_ONLY_CLUSTER;
         } else {
            this.unavailableViewSpec =
                  UnavailableViewSpec.SERVICES_VIEW_COMPUTE_ONLY_CLUSTER_NO_MOUNTED_REMOTE_DATASTORE;
         }
      } else if (this.hostCount > 0) {
         this.unavailableViewSpec = UnavailableViewSpec.VSAN_DISABLED;
      } else {
         this.unavailableViewSpec = UnavailableViewSpec.VSAN_DISABLED_NO_HOSTS;
      }
   }

   private registerQuickstartNotification(): void {
      try {
         this.quickstartStatusNotification.triggerRecalculate.next({
            isVsanEnabled: !!this.vsanConfig?.isEnabled,
            clusterData: null,
            clusterRef: this.clusterRef,
         });
      } catch (e) {
         this.errorMessages.push(e);
      }
   }

   fetchPrecheckUpgradeData(): void {
      this.upgradeService.getUpgradeStatus(this.clusterRef)
            .then(upgradeStatus => {
               // Create a new instance to trigger the upgrade component's refresh
               this.upgradeData = this.upgradeData.getNewInstance(upgradeStatus);
            })
            .catch(e => this.errorMessages.push(e));
   }

   fetchDisksUpgradeData(): void {
      this.upgradeData = null;
      this.upgradeService.getUpgradeData(this.clusterRef)
            .then(data => this.upgradeData = data)
            .catch(e => this.errorMessages.push(e));
   }

   private async fetchPerfServiceStatus(): Promise<void> {
      try {
         const result: VsanServiceData = await this.configService.getPerformanceStatus(this.clusterRef);
         this.perfSvsStatus = result.status;
         this.perfSvcConfig = result.details as PerfStatsObjectInfo;
         this.perfSvcEnabled = result.status === VsanServiceStatus.ENABLED;
      } catch (e) {
         this.errorMessages.push(e);
      }
   }

   private fetchCeipServiceEnabled(): Promise<any> {
      return this.ceipSvc.getCeipServiceEnabled(this.clusterRef)
            .then(result => this.ceipEnabled = result)
            .catch(e => this.supportInsightError = e);
   }

   private fetchStretchedClusterData(): Promise<any> {
      return this.stretchedClusterService.getAvailableDomains(this.clusterRef)
            .then(result => this.availableDomains = result)
            .catch(e => this.errorMessages.push(e));
   }

   private fetchIsStretchedCluster(): Promise<any> {
      return this.stretchedClusterService.getIsVsanStretchedCluster(this.clusterRef)
            .then(result => this.isStretchedCluster = result)
            .catch(e => this.errorMessages.push(e));
   }

   shutdownCluster(): void {
      new ModalBuilder(VsanRoutes.CLUSTER_POWER_OFF_WIZARD).setSize(950, 750).open();
   }

   turnOffVsan(): void {
      new ModalBuilder(VsanRoutes.TURN_OFF_VSAN)
            .setSize(600, 280)
            .open({
               clusterName: this.clusterName,
               isComputeOnlyCluster: this.isComputeOnlyCluster(),
            });
   }

   awaitThenInvoke = (taskRef: ManagedObject, callback: () => void) => {
      if (!taskRef) {
         callback();
      } else {
         this.taskService.getAwaitResult(taskRef)
               .then(() => callback.bind(this)())
               .catch(e => this.errorMessages.push(e));
      }
   };

   get hasHaDpmWarnings(): boolean {
      return !this.vsanConfig.isEnabled
            && (!this.clusterPowerState || ClusterPowerStateUtils.isClusterPoweredOn(this.clusterPowerState))
            && !this.isComputeOnlyCluster()
            && this.haDpmWarningMessages.length > 0;
   }

   get haDpmWarningMessages(): string[] {
      const warningMessages: string[] = [];
      if (this.isDPMEnabled) {
         warningMessages.push(VsanUiUtils.getString("vsan.services.unableToTurnOnVsan.reason.enabledDPM")
               .concat(this.EMPTY_SPACE,
                     VsanUiUtils.getString("vsan.services.unableToTurnOnVsan.reason.enabledDPM.suggestedAction")));
      }
      if (this.isHAEnabled) {
         warningMessages.push(VsanUiUtils.getString("vsan.services.unableToTurnOnVsan.reason.enabledHA")
               .concat(this.EMPTY_SPACE,
                     VsanUiUtils.getString("vsan.services.unableToTurnOnVsan.reason.enabledHA.suggestedAction")));
      }

      return warningMessages;
   }

   getHaDpmActionLinks(): AlertActionLink[] {
      const actionLinks: AlertActionLink[] = [];
      if (this.isDPMEnabled) {
         actionLinks.push({
            label: VsanUiUtils.getString("vsan.services.unableToTurnOnVsan.actionLink.goToDRS"),
            action: () => this.navigationService.navigateTo(PlatformExtension.CLUSTER_DRS, this.clusterRef),
         });
      }
      if (this.isHAEnabled) {
         actionLinks.push({
            label: VsanUiUtils.getString("vsan.services.unableToTurnOnVsan.actionLink.goToHA"),
            action: () => this.navigationService.navigateTo(PlatformExtension.CLUSTER_HA, this.clusterRef),
         });
      }

      return actionLinks;
   }

   get isTurnOffVsanEnabled(): boolean {
      return this.editClusterPermission
            && (this.isComputeOnlyCluster() || (!this.isDPMEnabled && !this.isHAEnabled));
   }

   getTurnOffVsanDisabledReason(): string {
      if (!this.editClusterPermission) {
         return VsanUiUtils.getString("vsan.common.nopermissions");
      } else if (!this.isComputeOnlyCluster()) {
         if (this.isHAEnabled && this.isDPMEnabled) {
            return VsanUiUtils.getString("vsan.services.unableToTurnOffVsan.reason.enabledHAandDPM");
         } else if (this.isHAEnabled) {
            return VsanUiUtils.getString("vsan.services.unableToTurnOffVsan.reason.enabledHA");
         } else if (this.isDPMEnabled) {
            return VsanUiUtils.getString("vsan.services.unableToTurnOffVsan.reason.enabledDPM");
         }
      }

      return null;
   }

   get isShutdownActionAvailable(): boolean {
      return ClusterPowerStateUtils.isClusterPoweredOn(this.clusterPowerState)
            && this.hostCount > 0;
   }

   get isShutdownActionEnabled(): boolean {
      return this.editClusterPermission
            && this.clusterPowerPermission
            && !this.isComputeOnlyCluster();
   }

   getShutdownDisabledReason(): string {
      if (!this.editClusterPermission || !this.clusterPowerPermission) {
         return VsanUiUtils.getString("vsan.common.nopermissions");
      } else if (this.isComputeOnlyCluster()) {
         return VsanUiUtils.getString("vsan.cluster.shutdown.wizard.computeOnly.disabled.reason");
      }
   }

   get isClusterChangingPowerState(): boolean {
      return ClusterPowerStateUtils.isClusterChangingPowerState(this.clusterPowerState);
   }

   private getServicesViewCards = (): Map<string, DragCardPosition> => {
      const result: Map<string, DragCardPosition> = new Map<string, DragCardPosition>();
      result.set(this.SUPPORT_INSIGHT_CARD, new DragCardPosition(CardContainer.LEFT, 0));
      result.set(this.PERFORMANCE_SERVICE_CARD, new DragCardPosition(CardContainer.LEFT, 1));
      result.set(this.FILE_SERVICE_CARD, new DragCardPosition(CardContainer.LEFT, 2));
      result.set(this.NETWORK_OPTIONS_CARD, new DragCardPosition(CardContainer.LEFT, 3));
      result.set(this.HISTORICAL_HEALTH_CARD, new DragCardPosition(CardContainer.LEFT, 4));
      result.set(this.ISCSI_SERVICE_CARD, new DragCardPosition(CardContainer.RIGHT, 0));
      result.set(this.DATA_SERVICES_CARD, new DragCardPosition(CardContainer.RIGHT, 1));
      result.set(this.CAPACITY_RESERVE_CARD, new DragCardPosition(CardContainer.RIGHT, 2));
      result.set(this.ADVANCED_OPTIONS_CARD, new DragCardPosition(CardContainer.RIGHT, 3));
      return result;
   };

   clusterPowerStateChanged(): void {
      this.isClusterPowerStateChanged = true;
      VsanUiUtils.globalRefresh();
   }

   private notifyClusterPowerStateChanged(): void {
      this.isClusterPowerStateChanged = false;
      if (ClusterPowerStateUtils.isClusterPoweredOn(this.clusterPowerState)) {
         this.successMessages.push(VsanUiUtils.getString("vsan.cluster.powerOn.message.success"));
      }
   }

   private handleVsanStatusChangeComplete(statusChangeData: VsanStatusChangeData): void {
      this.isEnableOrDisableVsanTaskFailed = statusChangeData.taskStatus === TaskStatus.ERROR;
      this.refresh();
   }
}
