import {Component, OnInit} from "@angular/core";
import {AlertType} from "@component/validation/alert-type";
import {RdmaData} from "@generated/rdma-data";
import {VsanRdmaService} from "@generated/vsan-rdma-service";
import {VsanStretchedClusterService} from "@generated/vsan-stretched-cluster-service";
import {EditNetworkContext} from "@navigation/context/edit-network-context";
import {DocumentationId} from "@navigation/model/documentation-id";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {ErrorHandler} from "@service/flow/error-handler.decorator";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {LoaderName} from "@service/flow/loader-name";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {ManagedObject} from "@service/managed-object";
import {SrMessageType} from "@service/screen-reader-announcer.service";
import {Icon} from "@util/icon";
import {VsanUiUtils} from "@util/vsan-util";
import {NetworkOptionsComponent} from "../../../services/network-options.component";

@Component({
   templateUrl: "edit-network.html",
   styleUrls: ["edit-network.scss"],
   selector: "vsan-edit-network",
})
export class EditNetworkComponent implements OnInit {
   clusterName: string;
   clusterRef: ManagedObject;

   @LoadingIndicator({name: [LoaderName.ON_INIT, LoaderName.ON_CONFIRM]})
   loading: boolean;

   @ErrorMessage({name: LoaderName.ON_INIT})
   alertMessage: string;

   rdmaData: RdmaData;
   originalIsRdmaEnabled: boolean;
   hasSharedWitnessHost: boolean;
   isStretchedClusterUnsupported: boolean;
   isLargeClusterSupportEnabled: boolean;

   documentationUrl: string;

   Icon: Icon = Icon;
   AlertType: typeof AlertType = AlertType;

   constructor(private rdmaService: VsanRdmaService,
         private stretchedClusterService: VsanStretchedClusterService,
         private navigationService: NavigationService) {
   }

   @Loader({
      name: LoaderName.ON_INIT,
      srMessage: "vsan.services.network.title",
      srMessageType: SrMessageType.PAGE_TITLE,
   })
   async ngOnInit(): Promise<void> {
      this.documentationUrl = this.navigationService.getVmwareHelpUrl(NavigationService.NETWORKING_URL,
            DocumentationId.RDMA_DOCUMENTATION);
      const context: EditNetworkContext = VsanUiUtils.getModalContext();
      this.clusterName = context.clusterName;
      this.clusterRef = context.clusterRef;
      this.isStretchedClusterUnsupported = context.isStretchedClusterUnsupported;
      this.isLargeClusterSupportEnabled = context.isLargeClusterSupportEnabled;
      [this.hasSharedWitnessHost, this.rdmaData] = await Promise.all([
         this.stretchedClusterService.hasSharedWitnessHost(this.clusterRef),
         this.rdmaService.getRdmaData(this.clusterRef),
      ]);
      this.originalIsRdmaEnabled = this.rdmaData.isRdmaEnabled;
      // Increase the dialog height when unsupported hosts are listed and there are no other config limitations
      if (this.showUnsupportedHosts()) {
         VsanUiUtils.setModalDialogHeight(NetworkOptionsComponent.RDMA_DIALOG_HEIGHT_UNSUPPORTED_HOSTS);
      }
   }

   get rdmaAlertMessage(): string {
      if (this.hasSharedWitnessHost) {
         return VsanUiUtils.getString("vsan.services.network.rdmaSupport.notSupportedSharedWitness");
      } else if (this.isStretchedClusterUnsupported) {
         return VsanUiUtils.getString("vsan.services.network.rdmaSupport.notSupportedStretchedCluster");
      } else if (this.isLargeClusterSupportEnabled) {
         return VsanUiUtils.getString("vsan.services.network.rdmaSupport.notSupportedLargeScaleCluster");
      } else if (this.hasUnsupportedHosts()) {
         return VsanUiUtils.getString("vsan.services.network.rdmaSupport.notSupportedByHostsHardware");
      }

      return VsanUiUtils.getString("vsan.services.network.rdmaSupport.hostsHardwareRdmaRequirement");
   }

   get rdmaAlertType(): string {
      if (this.hasConfigLimitation() || this.hasUnsupportedHosts()) {
         return AlertType.DANGER;
      }

      return AlertType.SUCCESS;
   }

   /**
    * This is to decide if UI should enable the toggle.
    */
   get isRdmaOptionEnabled(): boolean {
      // when isRdmaEnabled is already set to true but there is an unsupported cluster configuration,
      // allow user to disable the option. Otherwise disable the toggle and list the unsupported hosts
      return !this.hasUnsupportedHosts() && !this.hasConfigLimitation()
            ? true
            : this.rdmaData?.isRdmaEnabled;
   }

   showUnsupportedHosts(): boolean {
      return this.hasUnsupportedHosts() && !this.hasConfigLimitation();
   }

   navigateToHost(hostRef: ManagedObject): void {
      this.navigationService.navigateTo(PlatformExtension.HOST_PHYSICAL_ADAPTERS, hostRef);
      VsanUiUtils.closeModalDialog();
   }

   @Loader({name: LoaderName.ON_CONFIRM})
   async confirm(): Promise<void> {
      if (this.rdmaData.isRdmaEnabled === this.originalIsRdmaEnabled) {
         VsanUiUtils.closeModalDialog();
         return;
      }

      const task: ManagedObject = await this.rdmaService.configureVsanRdma(
            this.clusterRef, this.rdmaData.isRdmaEnabled);
      VsanUiUtils.closeModalDialog(task);
   }

   @ErrorHandler({name: LoaderName.ON_CONFIRM})
   private handleConfirmError(): void {
      this.alertMessage = VsanUiUtils.getString("vsan.services.network.edit.error");
   }

   // Check if any of the configurations that are not compatible with RDMA are enabled
   private hasConfigLimitation(): boolean {
      return this.isStretchedClusterUnsupported || this.hasSharedWitnessHost || this.isLargeClusterSupportEnabled;
   }

   private hasUnsupportedHosts(): boolean {
      return !VsanUiUtils.isEmpty(this.rdmaData?.unsupportedHosts);
   }
}
