import {ChangeDetectionStrategy, Component, Input} from "@angular/core";
import {Status} from "@component/card/status";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {PersistedProperty} from "@service/browser-storage.service";
import {KnowledgeBaseId} from "@navigation/model/knowledge-base-id";

@Component({
   selector: "vsan-support-insight",
   templateUrl: "support-insight.html",
   changeDetection: ChangeDetectionStrategy.OnPush,
})
export class SupportInsightComponent {
   Status: typeof Status = Status;
   readonly KnowledgeBaseId: typeof KnowledgeBaseId = KnowledgeBaseId;

   @Input()
   editClusterPermission: boolean;

   @PersistedProperty("vsan-support-insight-card-expanded-state")
   isExpanded: boolean;

   constructor(private navigationService: NavigationService) {
   }

   navigateToCeip = () => {
      this.navigationService.navigateWithoutContextObject(PlatformExtension.CEIP_VIEW);
   };
}