import {ChangeDetectionStrategy, Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {Status} from "@component/card/status";
import {VsanConfigSpec} from "@generated/vsan-config-spec";
import {EditVsanManagedDiskClaimContext} from "@navigation/context/edit-vsan-managed-disk-claim-context";
import {PersistedProperty} from "@service/browser-storage.service";
import {ManagedObject} from "@service/managed-object";
import {ModalBuilder} from "@util/modal-builder";
import {VsanDialogUtil} from "@util/vsan-dialog-util";
import {VsanRoutes} from "../../vsan-routes";
import {DiskManagementActionAvailabilityService} from "../disk-management/views/validation/disk-management-action-availability.service";
import {DiskManagementActionContext} from "../disk-management/views/validation/disk-management-action-context";
import {ServiceUtils} from "./service.utils";

@Component({
   selector: "vsan-max-settings",
   templateUrl: "vsan-max-settings.html",
   changeDetection: ChangeDetectionStrategy.OnPush,
})
export class VsanMaxSettingsComponent implements OnInit {

   @Input()
   clusterName: string;

   @Input()
   vsanConfig: VsanConfigSpec;

   @Input()
   editClusterPermission: boolean;

   @Input()
   isEmptyCluster: boolean;

   @Output()
   editTaskStarted: EventEmitter<ManagedObject> = new EventEmitter<ManagedObject>();

   @PersistedProperty("vsan-max-settings-card-expanded-state")
   isExpanded: boolean;

   vsanMaxStatus: Status;
   ServiceUtils: typeof ServiceUtils = ServiceUtils;

   constructor(private diskManagementActionAvailabilityService: DiskManagementActionAvailabilityService) {
   }

   ngOnInit(): void {
      this.vsanMaxStatus = this.vsanConfig?.isVsanMaxEnabled ? Status.Enabled : Status.Disabled;
   }

   isEditAllowed(): boolean {
      return this.diskManagementActionAvailabilityService.checkAvailability(
            this.getActionValidationContext()).available;
   }

   getDisabledEditReason(): string {
      return this.diskManagementActionAvailabilityService.checkAvailability(this.getActionValidationContext()).message;
   }

   private getActionValidationContext(): DiskManagementActionContext {
      return {
         actionId: DiskManagementActionAvailabilityService.EDIT_VSAN_MANAGED_DISK_CLAIM_ID,
         hasEditPermission: this.editClusterPermission,
         vsanConfig: this.vsanConfig,
         isEmptyCluster: this.isEmptyCluster,
         autoClaimMode: null,
         allHosts: null,
         clusterCapabilities: null,
         hasHostMaintenancePermission: null,
         spaceEfficiencyConfig: null,
      };
   }

   edit(): void {
      const context: EditVsanManagedDiskClaimContext = {clusterName: this.clusterName};
      new ModalBuilder(`${VsanRoutes.SERVICES_VIEW}/editVsanManagedDiskClaim`)
            .setSize(VsanDialogUtil.VSAN_DISK_CLAIM_ACTION_DIALOG.width,
                  VsanDialogUtil.VSAN_DISK_CLAIM_ACTION_DIALOG.height)
            .open(context)
            .then((taskRef: ManagedObject) => {
               this.editTaskStarted.emit(taskRef);
            });
   }
}