import {Component, OnInit} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {Logger} from "@util/logger";
import {GeneralConfigData} from "@component/general-settings/general-config.data";
import {VsanGeneralConfigService} from "@component/general-settings/vsan-general-config.service";
import {ConfigureVsanClusterMutationProvider} from "@generated/configure-vsan-cluster-mutation-provider";
import {VsanConfigSpec} from "@generated/vsan-config-spec";
import {VsanFileServiceConfigService} from "@generated/vsan-file-service-config-service";
import {FileServiceFeature} from "@generated/file-service-feature";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {ErrorHandler} from "@service/flow/error-handler.decorator";
import {SrMessageType} from "@service/screen-reader-announcer.service";
import {LoaderName} from "@service/flow/loader-name";

@Component({
   templateUrl: "edit-vsan-config.html",
   styleUrls: ["edit-vsan-config.scss"],
   selector: "vsan-edit-config",
})
export class EditVsanConfigComponent implements OnInit {
   contextObjectName: string;
   contextHeight: number;
   config: GeneralConfigData;
   vsanConfigSpec: VsanConfigSpec;
   validationErrors: string[] = [];

   @LoadingIndicator({name: [LoaderName.ON_INIT, LoaderName.ON_CONFIRM]})
   loading: boolean;
   hasInitializationError: boolean;
   showFileServiceOweDescription: boolean;
   clusterRef: ManagedObject;

   /**
    * Inject dataService to make calls to DataService
    */
   constructor(private vsanConfigService: VsanGeneralConfigService,
         private configureClusterMutationProvider: ConfigureVsanClusterMutationProvider,
         private fileServiceConfigService: VsanFileServiceConfigService) {
   }

   /**
    * Init the component's base data and fetch vsan config properties via dataService
    */
   @Loader({
      name: LoaderName.ON_INIT,
      srMessage: "vsan.generalConfigDialog.title",
      srMessageType: SrMessageType.PAGE_TITLE,
   })
   async ngOnInit(): Promise<void> {
      // Get the context object
      const context: any = VsanUiUtils.getModalContext();
      this.contextObjectName = context.clusterName;
      this.contextHeight = +context.height;
      if (!context.clusterId) {
         Logger.error("Unable to determine object ID!");
         return;
      }

      // Get vSAN configuration properties
      this.clusterRef = ManagedObject.fromUid(context.clusterId);
      [this.config, this.showFileServiceOweDescription] = await Promise.all([
         this.vsanConfigService.getVsanGeneralConfigData(this.clusterRef),
         this.isFileServiceOweSupported(this.clusterRef),
      ]);
      this.vsanConfigSpec = this.config.cloneVsanConfigSpec();
   }

   @ErrorHandler({name: LoaderName.ON_INIT})
   handleInitError(): void {
      this.hasInitializationError = true;
      this.validationErrors.push(VsanUiUtils.getString("vsan.common.error.dataExtraction"));
   }

   ok(): void {
      if (!this.config.isConfigChanged(this.vsanConfigSpec)) {
         // Do nothing if there is no change
         VsanUiUtils.closeModalDialog();
         return;
      }

      // Check for validation errors and show them
      this.validationErrors = this.config.validateConfig(this.vsanConfigSpec);
      if (!VsanUiUtils.isEmpty(this.validationErrors)) {
         // Do not proceed if there are any validation errors
         return;
      }

      // Post the changes to the mutation controller
      this.startTask();
   }

   private async isFileServiceOweSupported(clusterRef: ManagedObject): Promise<boolean> {
      const featureSupporteMap: any =
            await this.fileServiceConfigService.isFeatureSupportedOnRuntime(clusterRef,
                  [FileServiceFeature.OWE]);
      return featureSupporteMap[FileServiceFeature.OWE];
   }

   @Loader({name: LoaderName.ON_CONFIRM})
   private async startTask(): Promise<void> {
      const taskRef: any = await this.configureClusterMutationProvider.configure(this.clusterRef, this.vsanConfigSpec);
      VsanUiUtils.closeModalDialog(taskRef);
   }

   // Display errors in the wizard
   @ErrorHandler({name: LoaderName.ON_CONFIRM})
   private handleStartTaskError(error: any): void {
      this.validationErrors = [error];
   }
}
