import {ChangeDetectionStrategy, Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {PersistedProperty} from "@service/browser-storage.service";
import {Status} from "@component/card/status";
import {HistoricalHealthConfig} from "@generated/historical-health-config";
import {VsanUiUtils} from "@util/vsan-util";
import {Icon} from "@util/icon";
import {ManagedObject} from "@service/managed-object";
import {HistoricalHealthSettingsDialogComponent} from "../health/config/historical-health-settings-dialog.component";
import {ServiceUtils} from "./service.utils";

@Component({
   selector: "vsan-historical-health-settings",
   styleUrls: ["historical-health-settings.scss"],
   templateUrl: "historical-health-settings.html",
   changeDetection: ChangeDetectionStrategy.OnPush,
})
export class HistoricalHealthSettingsComponent implements OnInit {
   Icon: typeof Icon = Icon;
   VsanUiUtils: typeof VsanUiUtils = VsanUiUtils;

   @Input()
   config: HistoricalHealthConfig;

   @Input()
   clusterRef: ManagedObject;

   @Input()
   clusterName: string;

   @Input()
   editClusterPermission: boolean;

   @Output()
   editTaskStarted: EventEmitter<ManagedObject> = new EventEmitter<ManagedObject>();

   @PersistedProperty("vsan-historical-health-card-status")
   isExpanded: boolean;

   ngOnInit(): void {
      // Expand the card by default if the service is enabled
      if (this.config.isEnabled && VsanUiUtils.isUnset(this.isExpanded)) {
         this.isExpanded = true;
      }
   }

   getServiceStatus(): Status {
      return this.config.isEnabled ? Status.Enabled : Status.Disabled;
   }

   get unavailableMessage(): string {
      return !this.editClusterPermission ? VsanUiUtils.getString("vsan.common.nopermissions") : "";
   }

   editHistoricalHealthSettings(): void {
      HistoricalHealthSettingsDialogComponent.openDialog(this.clusterRef, this.clusterName, this.config)
         .then((taskRef: ManagedObject) => this.editTaskStarted.emit(taskRef));
   }
}