import {ChangeDetectionStrategy, Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {VsanConfigSpec} from "@generated/vsan-config-spec";
import {ManagedObject} from "@service/managed-object";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {SpaceEfficiencyService} from "@service/space-efficiency-service";
import {VsanUiUtils} from "@util/vsan-util";
import {ModalBuilder} from "@util/modal-builder";
import {Privileges} from "@generated/privileges";
import {ServiceUtils} from "./service.utils";
import {VsanDateTimeUtils} from "@util/vsan-dateteime-util";
import {PersistedProperty} from "@service/browser-storage.service";

@Component({
   selector: "vsan-data-services",
   templateUrl: "data-services.html",
   changeDetection: ChangeDetectionStrategy.OnPush,
})
export class DataServicesComponent implements OnInit {

   ServiceUtils: typeof ServiceUtils = ServiceUtils;
   formatMinutesInDaysOrHours: (minutes: number) => string = VsanDateTimeUtils.formatMinutes;

   @Input()
   clusterRef: ManagedObject;

   @Input()
   clusterName: string;

   @Input()
   vsanConfig: VsanConfigSpec;

   @Input()
   clusterCapabilityData: VsanCapabilityData;

   @Input()
   permissions: Map<string, boolean>;

   @Output()
   editTaskStarted: EventEmitter<ManagedObject> = new EventEmitter<ManagedObject>();

   @PersistedProperty("vsan-data-services-card-expanded-state", true)
   isExpanded: boolean;

   private editClusterPermission: boolean;
   private manageKeysPermission: boolean;
   private shallowRekeyPermission: boolean;

   constructor(private spaceEfficiencyService: SpaceEfficiencyService) {
   }

   ngOnInit(): void {
      this.editClusterPermission = this.permissions.get(Privileges.EDIT_CLUSTER());
      this.manageKeysPermission = this.permissions.get(Privileges.MANAGE_KEYS());
      this.shallowRekeyPermission = this.permissions.get(Privileges.SHALLOW_REKEY());
   }

   get editDisabledTitle(): string {
      if (!this.editClusterPermission) {
         return VsanUiUtils.getString("vsan.common.nopermissions");
      } else if (!this.spaceEfficiencyService.isSpaceEfficiencySupported(this.clusterCapabilityData)) {
         return VsanUiUtils.getString("vsan.services.not.supported");
      }
      return "";
   }

   get generateEncryptionKeysTitle(): string {
      if (!(this.editClusterPermission && this.manageKeysPermission || this.shallowRekeyPermission)) {
         return VsanUiUtils.getString("vsan.common.nopermissions");
      } else if (!this.vsanConfig.enableDataAtRestEncryption) {
         return VsanUiUtils.getString("vsan.common.service.disabled");
      }
      return "";
   }

   edit(): void {
      const eventTarget: EventTarget = event.target;
      const height: number = this.vsanConfig.autoClaimDisks ? 650 : 550;
      new ModalBuilder("services/editGeneralConfig")
            .setSize(700, height)
            .setEventTarget(eventTarget)
            .open({
               clusterId: ManagedObject.uid(this.clusterRef),
               clusterName: this.clusterName,
               height: height,
            })
            .then(taskRef => this.editTaskStarted.emit(taskRef));
   }

   generateEncryptionKeys(): void {
      const permissions: Map<string, boolean> = new Map([
         [Privileges.EDIT_CLUSTER(), this.editClusterPermission],
         [Privileges.MANAGE_KEYS(), this.manageKeysPermission],
      ]);
      new ModalBuilder("services/generateEncryptionKeys")
            .setSize(700, 315)
            .setTitle(VsanUiUtils.getString("vsan.encryption.rekey.dialog.title"))
            .open({
               eraseDisksBeforeUse: this.vsanConfig.eraseDisksBeforeUse,
               permissions: permissions,
            });
   }
}
