import {Component, EventEmitter, Input, OnInit, Output} from "@angular/core";
import {ClusterPowerService} from "@generated/cluster-power-service";
import {ManagedObject} from "@service/managed-object";
import {ClusterPowerState} from "@generated/cluster-power-state";
import {ClusterPowerStatus} from "@generated/cluster-power-status";
import {Icon} from "@util/icon";
import {ClusterPowerStateUtils} from "@util/cluster-power-state-utils";
import {VsanUiUtils} from "@util/vsan-util";
import {ReferenceWatcher} from "@util/reference-watcher";
import {TaskService} from "@generated/task-service";
import {TaskInfoData} from "@generated/task-info-data";

@Component({
   selector: "vsan-cluster-power-state-change-progress",
   templateUrl: "cluster-power-state-change-progress.html",
   styleUrls: ["cluster-power-state-change-progress.scss"],
   providers: [ReferenceWatcher],
})
export class ClusterPowerStateChangeProgressComponent implements OnInit {

   @Input()
   clusterRef: ManagedObject;

   @Output()
   clusterPowerStateChanged: EventEmitter<ClusterPowerState> = new EventEmitter<ClusterPowerState>();

   clusterPowerState: ClusterPowerState;
   taskInfo: TaskInfoData;

   private isVcOnVsan: boolean = false;
   private static readonly REFRESH_INTERVAL_MS: number = 2000;

   localizedSteps: Map<ClusterPowerStatus, string> = new Map<ClusterPowerStatus, string>([
      // Shutting down
      [
            ClusterPowerStatus.HA_DISABLED,
            VsanUiUtils.getString("vsan.cluster.shutdown.step.haDisabled"),
      ],
      [
            ClusterPowerStatus.SUPPORTED_VMS_POWERED_OFF,
            VsanUiUtils.getString("vsan.cluster.shutdown.step.supportedVMsPoweredOff"),
      ],
      [
            ClusterPowerStatus.VC_VM_POWERED_OFF,
            VsanUiUtils.getString("vsan.cluster.shutdown.step.vcVMPoweredOff"),
      ],
      [
            ClusterPowerStatus.VSAN_MEMBERSHIP_UPDATE_DISABLED,
            VsanUiUtils.getString("vsan.cluster.shutdown.step.vsanMemberShipUpdateDisabled"),
      ],
      [
            ClusterPowerStatus.VSAN_DOM_PAUSE_ALL_CCPS_SET,
            VsanUiUtils.getString("vsan.cluster.shutdown.step.vsanDOMPauseAllCCPsSet"),
      ],
      [
            ClusterPowerStatus.INFRA_VM_RECORDED,
            VsanUiUtils.getString("vsan.cluster.shutdown.step.infraVMsRecorded"),
      ],
      [
            ClusterPowerStatus.HOST_INFRA_VMS_POWERED_OFF,
            VsanUiUtils.getString("vsan.cluster.shutdown.step.infraVMsPoweredOff"),
      ],
      [
            ClusterPowerStatus.HOSTS_IN_MM,
            VsanUiUtils.getString("vsan.cluster.shutdown.step.hostsInMM"),
      ],
      [
            ClusterPowerStatus.HOSTS_POWER_OFF_INITIATED,
            VsanUiUtils.getString("vsan.cluster.shutdown.step.hostsPoweredOff"),
      ],
      [
            ClusterPowerStatus.CLUSTER_POWERED_OFF,
            VsanUiUtils.getString("vsan.cluster.shutdown.step.clusterPoweredOff"),
      ],
      // Powering on
      [
            ClusterPowerStatus.HOSTS_OUT_OF_MM,
            VsanUiUtils.getString("vsan.cluster.powerOn.step.hostsOutOfMM"),
      ],
      [
            ClusterPowerStatus.VSAN_DOM_PAUSE_ALL_CCPS_UNSET,
            VsanUiUtils.getString("vsan.cluster.powerOn.step.vsanDOMPauseAllCCPsUnset"),
      ],
      [
            ClusterPowerStatus.VSAN_MEMBERSHIP_UPDATE_ENABLED,
            VsanUiUtils.getString("vsan.cluster.powerOn.step.vsanMemberShipUpdateEnabled"),
      ],
      [
            ClusterPowerStatus.HOST_INFRA_VMS_POWERED_ON,
            VsanUiUtils.getString("vsan.cluster.powerOn.step.infraVMsPoweredOn"),
      ],
      [
            ClusterPowerStatus.VC_VM_POWERED_ON,
            VsanUiUtils.getString("vsan.cluster.powerOn.step.vcVMPoweredOn"),
      ],
      [
            ClusterPowerStatus.SUPPORTED_VMS_POWERED_ON,
            VsanUiUtils.getString("vsan.cluster.powerOn.step.supportedVMsPoweredOn"),
      ],
      [
            ClusterPowerStatus.HA_ENABLED,
            VsanUiUtils.getString("vsan.cluster.powerOn.step.haEnabled"),
      ],
      [
            ClusterPowerStatus.CLUSTER_POWERED_ON,
            VsanUiUtils.getString("vsan.cluster.powerOn.step.clusterPoweredOn"),
      ],
   ]);

   constructor(private clusterPowerService: ClusterPowerService,
         private refWatcher: ReferenceWatcher,
         private taskService: TaskService) { }

   async ngOnInit(): Promise<void> {
      await this.updatePowerStateProgress();
   }

   private async updatePowerStateProgress(): Promise<void> {
      this.clusterPowerState = await this.clusterPowerService.getPowerState(this.clusterRef);
      this.isVcOnVsan = this.checkForVcOnVsan();

      if (!ClusterPowerStateUtils.isClusterChangingPowerState(this.clusterPowerState)) {
         // Cluster is no longer changing its state, so this component is no longer needed.
         this.clusterPowerStateChanged.emit(this.clusterPowerState);
         return;
      }

      if (this.clusterPowerState.taskRef) {
         this.taskInfo = await this.taskService.getInfo(this.clusterPowerState.taskRef);
         this.refWatcher.setTimeout(
               async () => await this.updatePowerStateProgress(),
               ClusterPowerStateChangeProgressComponent.REFRESH_INTERVAL_MS,
         );
      } else {
         this.taskInfo = null;
      }
   }

   async resume(): Promise<void> {
      await this.clusterPowerService.resumePowerStateChange(this.clusterRef);
      await this.updatePowerStateProgress();
   }

   get processLabel(): string {
      if (!this.clusterPowerState) {
         return "";
      }

      if (ClusterPowerStateUtils.isClusterShuttingDown(this.clusterPowerState)) {
         return !this.clusterPowerState.taskRef
            ? VsanUiUtils.getString("vsan.cluster.shutdown.label.paused")
            : VsanUiUtils.getString("vsan.cluster.shutdown.label.running");
      } else {
         return !this.clusterPowerState.taskRef
            ? VsanUiUtils.getString("vsan.cluster.powerOn.label.paused")
            : VsanUiUtils.getString("vsan.cluster.powerOn.label.running");
      }
   }

   get resumeLabel(): string {
      return ClusterPowerStateUtils.isClusterShuttingDown(this.clusterPowerState)
         ? VsanUiUtils.getString("vsan.cluster.shutdown.label.resume")
         : VsanUiUtils.getString("vsan.cluster.powerOn.label.resume");
   }

   get resumeDescription(): string {
      return ClusterPowerStateUtils.isClusterShuttingDown(this.clusterPowerState)
         ? VsanUiUtils.getString("vsan.cluster.shutdown.description.resume")
         : VsanUiUtils.getString("vsan.cluster.powerOn.description.resume");
   }

   getStatusLabel(step: ClusterPowerStatus): string {
      if (step === ClusterPowerStatus.SUPPORTED_VMS_POWERED_OFF) {
         // We have a different label for the VC on vSAN case, so the map above is not correct.
         return this.isVcOnVsan ? VsanUiUtils.getString("vsan.cluster.shutdown.step.supportedVMsPoweredOff.vcOnVsan")
               :  VsanUiUtils.getString("vsan.cluster.shutdown.step.supportedVMsPoweredOff");
      }
      return this.localizedSteps.get(step);
   }

   getStepIconClass(stepIndex: number): string {
      if (this.isStepCompleted(stepIndex)) {
         return Icon.CLR_SUCCESS_ICON_CLASS;
      } else if (this.isStepFailed(stepIndex)) {
         return Icon.CLR_ERROR_ICON_CLASS;
      }
   }

   getStepIconShape(stepIndex: number): string {
      if (this.isStepCompleted(stepIndex)) {
         return Icon.CLR_SUCCESS_STANDARD_ICON_SHAPE;
      } else if (this.isStepFailed(stepIndex)) {
         return Icon.CLR_ERROR_STANDARD_ICON_SHAPE;
      } else {
         return Icon.EMPTY_ICON;
      }
   }

   isStepCompleted(stepIndex: number): boolean {
      return stepIndex < this.getCurrentlyRunningStepIndex();
   }

   isStepFailed(stepIndex: number): boolean {
      return this.isCurrentStep(stepIndex) && !!this.clusterPowerState?.errorMessage;
   }

   isStepInProgress(stepIndex: number): boolean {
      return this.isCurrentStep(stepIndex) && !this.clusterPowerState?.errorMessage;
   }

   private isCurrentStep(stepIndex: number): boolean {
      return this.getCurrentlyRunningStepIndex() === stepIndex;
   }

   private getCurrentlyRunningStepIndex(): number {
      return this.clusterPowerState?.inProgressStatuses.indexOf(this.clusterPowerState?.currentOperation);
   }

   private checkForVcOnVsan(): boolean {
      return this.clusterPowerState?.inProgressStatuses?.includes(ClusterPowerStatus.VC_VM_POWERED_OFF)
         || this.clusterPowerState?.inProgressStatuses?.includes(ClusterPowerStatus.VC_VM_POWERED_ON);
   }
}