import {Component, Input, OnInit} from "@angular/core";
import {VsanFileServiceDomain} from "@generated/vsan-file-service-domain";
import {VsanFileServiceLastDomainFailureReport} from "@generated/vsan-file-service-last-domain-failure-report";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {VsanUiUtils} from "@util/vsan-util";
import {ManagedObject} from "@service/managed-object";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {VsanServiceData} from "@generated/vsan-service-data";
import {VsanServiceStatus} from "@generated/vsan-service-status";
import {VsanVdfsConfig} from "@generated/vsan-vdfs-config";
import {VsanFileServicePrecheckResult} from "@generated/vsan-file-service-precheck-result";
import {DisableFileServicesModalContext} from "@navigation/context/disable-file-services-modal-context";
import {ModalBuilder} from "@util/modal-builder";
import {FileServicesConfigureWizardModalContext} from "@navigation/context/file-services-configure-wizard-modal-context.data";
import {Privileges} from "@generated/privileges";
import {EnableFileServiceModalContext} from "../file-services/enable-service/enable-file-service-modal-context.data";
import {ServiceUtils} from "./service.utils";
import {VsanConfigService} from "@generated/vsan-config-service";
import {TaskMonitorService} from "@service/task-monitor-service";
import {PersistedProperty} from "@service/browser-storage.service";
import {VsanFileServiceConfigService} from "@generated/vsan-file-service-config-service";
import {Version} from "@util/version";
import {FileServiceUtil} from "../file-services/file-service-util";
import {DomainOrHostData} from "@generated/domain-or-host-data";
import {UpgradeFileServiceModalContext} from "../file-services/upgrade/upgrade-file-service-modal-context";
import {AlertType} from "@component/validation/alert-type";
import {VsanFileServiceOvfProps} from "@generated/vsan-file-service-ovf-props";
import {Logger} from "@util/logger";
import {IpSettingData} from "@component/configurations/file-service-configuration.component";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {SrMessageType} from "@service/screen-reader-announcer.service";

@Component({
   selector: "vsan-file-service",
   templateUrl: "file-service.html",
   styleUrls: ["file-service.scss"],
})
export class FileServiceComponent implements OnInit {
   ServiceUtils: typeof ServiceUtils = ServiceUtils;
   AlertType: typeof AlertType = AlertType;

   @Input()
   clusterRef: ManagedObject;

   @Input()
   clusterName: string;

   @Input()
   clusterPermissions: Map<string, boolean>;

   @Input()
   vcPermissions: Map<string, boolean>;

   @Input()
   vcCapabilityData: VsanCapabilityData;

   @Input()
   latestDiskFmtVersion: string;

   @Input()
   minDiskFmtVersion: string;

   @Input()
   availableDomains: DomainOrHostData[];

   @Input()
   isStretchedCluster: boolean;

   @Input()
   isPerfServiceEnabled: boolean;

   @PersistedProperty("vsan-file-service-card-expanded-status")
   isExpanded: boolean;

   status: VsanServiceStatus;
   vdfsConfig: VsanVdfsConfig;
   ipSettings: IpSettingData[] = [];
   affinitySiteSupported: boolean;
   adConfigSupported: boolean;
   largeScaleClusterSupported: boolean;

   @ErrorMessage()
   errorMessage: string;

   @LoadingIndicator()
   loading: boolean;

   isFsDomainMissing: boolean;
   upgradeFsvmTask: ManagedObject;
   // Keep this one public as it is passed down to the performance service validation banner.
   editClusterPermission: boolean;
   private vcServerPermission: boolean;
   // hasLatestOvf is used to show/hide the ovf page in wizard
   private hasLatestOvf: boolean;
   private latestOvfVersion: Version;
   private fsvmVersion: Version;
   private vcVersion: Version;
   private hostVersion: Version;

   private lastFailedDomainConfig: VsanFileServiceDomain;
   private advancedPreflightCheckSupported: boolean;

   preflightCheckErrors: string[];

   constructor(private configService: VsanConfigService,
         private taskService: TaskMonitorService,
         private fileService: VsanFileServiceConfigService,
         private navigationService: NavigationService) {
   }

   ngOnInit(): void {
      this.editClusterPermission = this.clusterPermissions.get(Privileges.EDIT_CLUSTER());
      this.vcServerPermission = this.vcPermissions.get(Privileges.GLOBAL_VCSERVER());
   }

   @Loader({srMessage: "vsan.services.fileservices.title", srMessageType: SrMessageType.DATA})
   async onFileServiceStatusLoaded(fileServiceData: VsanServiceData): Promise<void> {
      if (this.status && fileServiceData.status !== this.status) {
         // refresh the TOC view to show/hide the File Shares link in the left navigation
         this.status = null;
         VsanUiUtils.globalRefresh();
         return;
      }
      this.status = fileServiceData.status;
      // Expand the card by default if the service is enabled
      if (this.status === VsanServiceStatus.ENABLED && VsanUiUtils.isUnset(this.isExpanded)) {
         this.isExpanded = true;
      }
      this.vdfsConfig = fileServiceData.details as VsanVdfsConfig;
      // this.vdfsConfig === null means the hosts in this cluster are low version, stop here to avoid UI errors
      if (!this.vdfsConfig) {
         return;
      }
      this.isFsDomainMissing = !this.vdfsConfig.config?.domainConfig;
      this.fsvmVersion = this.vdfsConfig.precheckResult
            ? new Version(this.vdfsConfig.precheckResult.fsvmVersion)
            : null;
      this.vcVersion = new Version(this.vdfsConfig.precheckResult.vcVersion);
      this.hostVersion = new Version(this.vdfsConfig.precheckResult.hostVersion);
      this.adConfigSupported = this.vdfsConfig.precheckResult.activeDirectorySupported;
      this.affinitySiteSupported = this.vdfsConfig.precheckResult.affinitySiteSupported && this.isStretchedCluster;
      this.largeScaleClusterSupported = this.vdfsConfig.precheckResult.largeScaleClusterSupported;
      this.advancedPreflightCheckSupported = this.vdfsConfig.precheckResult.advancedPreflightCheckSupported;
      this.ipSettings = FileServiceUtil.getIpSettings(this.vdfsConfig, this.availableDomains);
      await Promise.all([
            this.loadLastDomainFailureReport(),
            this.updateOvfData(),
      ]);
   }

   /**
    * Check the capability and load the last failed domain config if API supports
    */
   private async loadLastDomainFailureReport(): Promise<void> {
      if (!this.advancedPreflightCheckSupported) {
         return;
      }
      this.preflightCheckErrors = [];
      const failureReport: VsanFileServiceLastDomainFailureReport =
            await this.fileService.getLastDomainFailureReport(this.clusterRef);
      this.lastFailedDomainConfig = failureReport.domainConfig;
      if (this.lastFailedDomainConfig) {
         // In edit FS domain workflow, the adConfig is not saved if there is no AD change, so we have to set it back
         this.lastFailedDomainConfig.adConfig = this.vdfsConfig.config.domainConfig?.adConfig;
         // only set the message when there is a domain config failure
         this.preflightCheckErrors = [
               VsanUiUtils.getString("vsan.services.fileservices.wizard.preflightcheck.error"),
               failureReport.error,
         ];
      }
   }

   /**
    * Load the OVF properties, and don't throw exception if any error occurs
    * because this only impact the 'Check Upgrade' button
    */
   private async getOvfProps(): Promise<VsanFileServiceOvfProps> {
      try {
         return await this.fileService.getPublicOvfProps(this.clusterRef);
      } catch (e) {
         Logger.error(e);
      }
      return null;
   }

   /**
    * Parse the latest Ovf download URL to get the OVF version, and compare
    * with the current OVF version to determine if the OVF can be upgraded.
    * Also set this.hasLatestOvf and use it to show/hide the ovf page in FS wizard.
    */
   private async updateOvfData(): Promise<void> {
      const ovfProps: VsanFileServiceOvfProps = await this.getOvfProps();
      if (ovfProps && !VsanUiUtils.isEmpty(ovfProps.url)) {
         this.latestOvfVersion = Version.fromFilePathOrName(ovfProps.url);
         const existingOvfVersion: Version = new Version(this.vdfsConfig.precheckResult.ovfInstalled);
         this.hasLatestOvf = Version.isSame(this.latestOvfVersion, existingOvfVersion);
      }
   }

   get enableBtnDisabled(): boolean {
      return !this.editClusterPermission || !this.vcServerPermission || !!this.vdfsPrecheckIssue;
   }

   get showPerfEnableWarning(): boolean {
      return !this.isPerfServiceEnabled &&
            this.vdfsConfig && this.vdfsConfig.config && this.vdfsConfig.config.isFileAnalyticsEnabled;
   }

   get enableDisabledTitle(): string {
      if (!this.vcServerPermission || !this.editClusterPermission) {
         return VsanUiUtils.getString("vsan.common.nopermissions");
      }
      return this.vdfsPrecheckIssue;
   }

   get commonDisabledTitle(): string {
      if (!this.editClusterPermission) {
         return VsanUiUtils.getString("vsan.common.nopermissions");
      }
      return "";
   }

   get upgradeDisabledReason(): string {
      // Don't allow user to upgrade the OVF if the FS domain contains old host
      const hasOldHost: boolean = Version.isLarger(this.vcVersion, this.hostVersion);
      // Don't allow user to upgrade the OVF if the disk format version is old to avoid some unexpected errors,
      // this is a limitation from the backend API
      const oldDiskFormatVersion: boolean =
            parseInt(this.latestDiskFmtVersion, 0) > parseInt(this.minDiskFmtVersion, 0);

      const isLatestOvfVersionInstalled: boolean = this.latestOvfVersion
            && !Version.isLarger(this.latestOvfVersion, this.fsvmVersion);

      if (!this.vcServerPermission || !this.editClusterPermission) {
         return VsanUiUtils.getString("vsan.common.nopermissions");
      } else if (this.isFsDomainMissing && !this.vcCapabilityData.isSeparatingFileServiceWorkflowSupported) {
         // TODO: [rogerw] remove this else if after this feature is tested
         return VsanUiUtils.getString("vsan.fileservices.upgrade.noDomain.error");
      } else if (hasOldHost) {
         return VsanUiUtils.getString("vsan.fileservices.upgrade.oldHost");
      } else if (oldDiskFormatVersion) {
         return VsanUiUtils.getString("vsan.fileservices.upgrade.dfc");
      } else if (isLatestOvfVersionInstalled) {
         return VsanUiUtils.getString("vsan.fileservices.upgrade.latest.desc");
      }
      return null;
   }

   get vdfsPrecheckIssue(): string {
      if (!this.vdfsConfig) {
         return "";
      }
      const precheck: VsanFileServicePrecheckResult = this.vdfsConfig.precheckResult;
      if (!precheck) {
         return "";
      } else if (precheck.mixedModeIssue) {
         return precheck.mixedModeIssue;
      } else if (precheck.networkPartitionIssue) {
         return precheck.networkPartitionIssue;
      } else if (precheck.vsanDatastoreIssue) {
         return precheck.vsanDatastoreIssue;
      } else if (precheck.dvsConfigIssue) {
         return precheck.dvsConfigIssue;
      } else {
         return "";
      }
   }

   navigateToShares(): void {
      this.navigationService.navigateTo(PlatformExtension.FILE_SERVICE_SHARES);
   }

   enableFileService(): void {
      const vsanVdfsConfig: VsanVdfsConfig = JSON.parse(JSON.stringify(this.vdfsConfig));
      const enableDialogContext: EnableFileServiceModalContext = {
         network: vsanVdfsConfig?.config?.network,
         precheckResult: vsanVdfsConfig?.precheckResult,
      } as EnableFileServiceModalContext;
      new ModalBuilder("enableFileService")
            .setDimensions(ModalBuilder.DEFAULT_SIZE)
            .open(enableDialogContext)
            .then((taskRef: ManagedObject) => {
               this.awaitThenInvoke(this.refresh, taskRef);
            });
   }

   editFileServices(): void {
      // TODO: [rogerw] remove this after this feature is tested
      if (ServiceUtils.isDisabled(this.status) && this.vcCapabilityData.isSeparatingFileServiceWorkflowSupported) {
         return this.enableFileService();
      }

      // We need a deep clone of the object so if the user cancels the wizard,
      // no changes are saved in the original object.
      const vsanVdfsConfig: VsanVdfsConfig = JSON.parse(JSON.stringify(this.vdfsConfig));
      if (this.lastFailedDomainConfig) {
         vsanVdfsConfig.config.domainConfig = this.lastFailedDomainConfig;
      }
      const context: FileServicesConfigureWizardModalContext = {
         clusterRef: this.clusterRef,
         config: vsanVdfsConfig ? vsanVdfsConfig.config : null,
         isEdit: !ServiceUtils.isDisabled(this.status),
         capabilityData: this.vcCapabilityData,
         fileSharesCount: vsanVdfsConfig ? vsanVdfsConfig.numberOfShares : 0,
         hasLastestOvf: this.hasLatestOvf,
         ovfVersion: vsanVdfsConfig.precheckResult.ovfInstalled,
         largeScaleClusterSupported: this.largeScaleClusterSupported,
         hasAdConfigured: !!this.vdfsConfig?.config?.domainConfig?.adConfig?.domain,
         lastDomainConfigErrors: this.preflightCheckErrors,
      };

      new ModalBuilder("configureFileServices")
            .setDimensions(ModalBuilder.WIZARD_XL)
            .open(context)
            .then((taskRefs: ManagedObject[] | ManagedObject) => {
               // enable fs wizard returns a single task, but edit fs configuration wizard returns a task array
               if (taskRefs instanceof Array) {
                  this.awaitThenInvoke(this.refresh, ...taskRefs);
               } else {
                  this.awaitThenInvoke(this.refresh, taskRefs);
               }
            });
   }

   disableFileServices(): void {
      const context: DisableFileServicesModalContext = {
         clusterName: this.clusterName,
         clusterRef: this.clusterRef,
         isEnabled: true,
         fileShareCount: this.vdfsConfig.numberOfShares,
      };

      new ModalBuilder("disableFileServices")
            .setSize(600, 350)
            .open(context)
            .then((taskRef: ManagedObject) => this.awaitThenInvoke(this.refresh, taskRef));
   }

   upgradeFileServices(): void {
      const context: UpgradeFileServiceModalContext = {
         domainConfig: this.vdfsConfig.config.domainConfig,
         precheckResult: this.vdfsConfig.precheckResult,
      };
      new ModalBuilder("upgradeFileServices")
            .setSize(700, 550)
            .open(context)
            .then((taskRef: ManagedObject) => {
               this.upgradeFsvmTask = taskRef;
               this.awaitThenInvoke(this.refresh, taskRef);
            });
   }

   private awaitThenInvoke = (fetchMethod: () => any, ...taskRefs: ManagedObject[]) => {
      const runningTasks: Promise<any>[] =
            [...taskRefs].map(taskRef => this.taskService.getAwaitResult(taskRef));
      Promise.all(runningTasks)
            .then(() => fetchMethod.call(this))
            .catch(e => this.errorMessage = e);
   };

   private refresh = () => {
      VsanUiUtils.globalRefresh();
   };
}
