import {VsanUiUtils} from "@util/vsan-util";

/**
 * Parse version string to a comparable object
 * '7.0.1.1000-39027875' => 'major.minor.revision.patchLevel-build'
 */
export class Version {
   static readonly OVF_VERSION_PATTERN: RegExp = /.+(\d+\.\d+\.\d+\.\d+-\d+)_.+\.ovf/;

   major: number;
   minor: number;
   revision: number;
   patchLevel: number;
   build: number;

   constructor(version: string) {
      if (VsanUiUtils.isEmpty(version)) {
         return;
      }
      let parts: string[] = version.split("-");
      if (parts.length > 1) {
         this.build = parseInt(parts[1], 0);
      }
      parts = parts[0].split(".");
      this.major = parseInt(parts[0], 0);
      if (parts.length > 1) {
         this.minor = parseInt(parts[1], 0);
         if (parts.length > 2) {
            this.revision = parseInt(parts[2], 0);
         }
         if (parts.length > 3) {
            this.patchLevel = parseInt(parts[3], 0);
         }
      }
   }

   static isLarger(v1: Version, v2: Version): boolean {
      return !!v1 && v1.compare(v2) > 0;
   }

   static isSame(v1: Version, v2: Version): boolean {
      return (!v1 && !v2) || (!!v1 && v1.compare(v2) === 0);
   }

   /**
    * Check if the v1 version is newer major version than the v2 version.
    * We don't compare the build number here because for major version it doesn't have build info.
    */
   static isNewMajorRelease(v1: Version, v2: Version): boolean {
      return v1.major > v2.major || v1.minor > v2.minor || v1.revision > v2.revision;
   }

   /**
    * Method to get the ovf file version from the file name or path
    */
   static fromFilePathOrName(ovfFilePathOrName: string): Version {
      return new Version(this.OVF_VERSION_PATTERN.exec(ovfFilePathOrName)[1]);
   }

   compare(that: Version): number {
      const orderedFields: string[] = ["major", "minor", "revision", "patchLevel", "build"];
      for (let i: number = 0; i <= orderedFields.length; i++) {
         const fieldName: string = orderedFields[i];
         if (this[fieldName] !== that[fieldName]) {
            return this[fieldName] > that[fieldName] ? 1 : -1;
         }
      }
      return 0;
   }

   toString(): string {
      let versionStr: string = `${this.major}.${this.minor}.${this.revision}`;
      if (this.patchLevel) {
         versionStr += `.${this.patchLevel}`;
      }
      if (this.build) {
         versionStr += `-${this.build}`;
      }
      return versionStr;
   }
}
