import {Component, OnInit} from "@angular/core";
import {VsanConfigService} from "@generated/vsan-config-service";
import {VsanConfigSpec} from "@generated/vsan-config-spec";
import {VsanMaxSettingsService} from "@generated/vsan-max-settings-service";
import {EditVsanManagedDiskClaimContext} from "@navigation/context/edit-vsan-managed-disk-claim-context";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {LoaderName} from "@service/flow/loader-name";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {ManagedObject} from "@service/managed-object";
import {SrMessageType} from "@service/screen-reader-announcer.service";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "edit-vsan-managed-disk-claim.html",
   selector: "vsan-edit-vsan-managed-disk-claim",
})
export class EditVsanManagedDiskClaim implements OnInit {

   clusterName: string;
   isDiskClaimVsanManaged: boolean;
   vsanConfig: VsanConfigSpec;

   @LoadingIndicator({name: [LoaderName.ON_INIT, LoaderName.ON_CONFIRM]})
   loading: boolean;

   @ErrorMessage({name: [LoaderName.ON_INIT, LoaderName.ON_CONFIRM]})
   alertMessage: string;

   constructor(private vsanConfigService: VsanConfigService, private vsanMaxSettingsService: VsanMaxSettingsService) {
   }

   @Loader({
      name: LoaderName.ON_INIT,
      srMessage: "vsan.services.vsan.max.diskClaim.label",
      srMessageType: SrMessageType.PAGE_TITLE,
   })
   async ngOnInit(): Promise<void> {
      const context: EditVsanManagedDiskClaimContext = VsanUiUtils.getModalContext();
      this.clusterName = context.clusterName;
      this.vsanConfig = await this.vsanConfigService.getVsanConfigSpec(ManagedObject.contextObject);
      // Copy the value, so the config object can be used to check for changes to the value.
      this.isDiskClaimVsanManaged = this.vsanConfig.isDiskClaimVsanManaged;
   }

   @Loader({name: LoaderName.ON_CONFIRM})
   async confirm(): Promise<void> {
      if (this.isDiskClaimVsanManaged === this.vsanConfig.isDiskClaimVsanManaged) {
         VsanUiUtils.closeModalDialog();
         return;
      }
      const task: ManagedObject = await this.vsanMaxSettingsService.configureVsanMaxSettings(
            ManagedObject.contextObject,
            this.isDiskClaimVsanManaged);
      VsanUiUtils.closeModalDialog(task);
   }

}