import {Component, OnInit, ViewChild} from "@angular/core";
import {
   AbstractControl,
   FormBuilder,
   FormControl,
   FormGroup,
   ValidationErrors,
   ValidatorFn,
   Validators,
} from "@angular/forms";
import {ClrForm} from "@clr/angular";
import {PbmDataProvider} from "@generated/pbm-data-provider";
import {StoragePolicyData} from "@generated/storage-policy-data";
import {VsanIscsiAuthSpec} from "@generated/vsan-iscsi-auth-spec";
import {VsanIscsiConfigEditSpec} from "@generated/vsan-iscsi-config-edit-spec";
import {VsanIscsiMutationProvider} from "@generated/vsan-iscsi-mutation-provider";
import {VsanIscsiPropertyProvider} from "@generated/vsan-iscsi-property-provider";
import {VsanIscsiTargetConfig} from "@generated/vsan-iscsi-target-config";
import {ErrorHandler} from "@service/flow/error-handler.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {ManagedObject} from "@service/managed-object";
import {SrMessageType} from "@service/screen-reader-announcer.service";
import {ReactiveFormUtil} from "@util/reactive-form-util";
import {VsanIscsiUtil} from "@util/vsan-iscsi-util";
import {VsanUiUtils} from "@util/vsan-util";
import IscsiTargetAuthType = VsanIscsiTargetConfig.IscsiTargetAuthType;

@Component({
   templateUrl: "edit-iscsi-target-config.html",
   styleUrls: ["edit-iscsi-target-config.scss"],
   selector: "vsan-edit-iscsi-config",
})
export class EditIscsiTargetComponent implements OnInit {

   VsanUiUtils: typeof VsanUiUtils = VsanUiUtils;

   static readonly DEFAULT_HEIGHT: number = 400;
   private static readonly MIDDLE_HEIGHT: number = 580;
   private static readonly EXPANDED_HEIGHT: number = 680;

   iscsiSettings: FormGroup;

   @LoadingIndicator()
   loading: boolean;

   alertMessages: string[] = [];
   clusterName: string;
   policyMessage: string;

   private isEmptyCluster: boolean;
   availableNetworks: string[];
   availablePolicies: StoragePolicyData[];

   private clusterRef: ManagedObject;

   originalIsServiceEnabled: boolean;

   isServiceEnabled: boolean;

   @ViewChild(ClrForm)
   iscsiSettingsForm: ClrForm;

   constructor(private iscsiProvider: VsanIscsiPropertyProvider,
         private pbmProvider: PbmDataProvider,
         private iscsiMutationProvider: VsanIscsiMutationProvider,
         private formBuilder: FormBuilder) {
   }

   @Loader({
      srMessage: "vsan.iscsi.action.edit.title",
      srMessageType: SrMessageType.PAGE_TITLE,
   })
   async ngOnInit(): Promise<void> {
      const context: any = VsanUiUtils.getModalContext();
      this.clusterRef = context.clusterRef;
      this.clusterName = context.clusterName;

      let iscsiConfig: VsanIscsiTargetConfig;
      [iscsiConfig, this.availableNetworks, this.availablePolicies] = await Promise.all([
         this.iscsiProvider.getVsanIscsiTargetConfig(this.clusterRef),
         this.iscsiProvider.getHostsCommonVnicList(this.clusterRef),
         this.pbmProvider.getStoragePolicies(this.clusterRef),
      ]);
      if (VsanUiUtils.isEmpty(this.availableNetworks)) {
         this.alertMessages.push(VsanUiUtils.getString("vsan.iscsi.network.none"));
      }
      this.handleConfig(iscsiConfig);
   }

   private handleConfig = (iscsiConfig: VsanIscsiTargetConfig): void => {
      let network: string = "";
      let port: number = VsanIscsiUtil.DEFAULT_PORT;
      let authSpec: VsanIscsiAuthSpec = null;

      if (iscsiConfig) {
         this.originalIsServiceEnabled = iscsiConfig.status;
         this.isServiceEnabled = true;
         network = iscsiConfig.network;
         port = iscsiConfig.port || VsanIscsiUtil.DEFAULT_PORT;

         this.isEmptyCluster = iscsiConfig.emptyCluster;
         if (this.isEmptyCluster) {
            this.alertMessages.push(VsanUiUtils.getString("vsan.iscsi.cluster.empty"));
         }

         if (iscsiConfig.authType) {
            authSpec = VsanIscsiAuthSpec.Factory.create(
                  iscsiConfig.authType.toString(),
                  iscsiConfig.outgoingUser,
                  iscsiConfig.outgoingSecret,
                  iscsiConfig.incomingUser,
                  iscsiConfig.incomingSecret);
         }
      }

      const policy: StoragePolicyData = this.pickInitialPolicy(iscsiConfig);

      // Preselect the first network if no network configured for iscsi yet.
      if (!network && this.availableNetworks && this.availableNetworks.length > 0) {
         network = this.availableNetworks[0];
      }

      this.iscsiSettings = this.formBuilder.group({
         network: new FormControl(network, [Validators.required]),
         port: new FormControl(port, [
            Validators.required,
            Validators.min(VsanIscsiUtil.TCP_PORT_MIN),
            Validators.max(VsanIscsiUtil.TCP_PORT_MAX),
         ]),
         authSpec: new FormControl(authSpec),
         policy: new FormControl(policy, [this.policyValidator]),
      });

      ReactiveFormUtil.validateControls([this.iscsiSettings.controls.policy as FormControl]);
   };

   private pickInitialPolicy(iSCSIConfig: VsanIscsiTargetConfig): StoragePolicyData {
      let initialPolicy: StoragePolicyData = null;

      if (VsanUiUtils.isEmpty(this.availablePolicies)) {
         return initialPolicy;
      }

      if (iSCSIConfig && iSCSIConfig.status) {
         if (iSCSIConfig.homeObjectStorageProfile) {
            initialPolicy = this.availablePolicies
                  .find(policy => policy.id === iSCSIConfig.homeObjectStorageProfile.id);
         }
         // Don't preselect the default policy if the iSCSI target service is enabled but without home object.
      } else {
         // Preselect the default policy when there isn't iSCSI config or there is such one but the status is disabled.
         initialPolicy = this.availablePolicies.find(policy => policy.isDefault);
      }

      return initialPolicy;
   }

   handleAuthChange = (type: IscsiTargetAuthType) => {
      switch (type) {
         case IscsiTargetAuthType.CHAP:
            VsanUiUtils.setModalDialogHeight(EditIscsiTargetComponent.MIDDLE_HEIGHT);
            break;
         case IscsiTargetAuthType.CHAP_Mutual:
            VsanUiUtils.setModalDialogHeight(EditIscsiTargetComponent.EXPANDED_HEIGHT);
            break;
         default:
            VsanUiUtils.setModalDialogHeight(EditIscsiTargetComponent.DEFAULT_HEIGHT);
      }
      // After changing the authentication selection, the form has been touched, but sometimes its touched property
      // has no chagne (for example, changing authentication from None to CHAP directly), so reset it manually.
      this.iscsiSettings.markAsTouched();
   };

   confirm = () => {
      this.clearAlert();
      if (this.isEmptyCluster) {
         this.alertMessages = [VsanUiUtils.getString("vsan.iscsi.cluster.empty")];
         return;
      }

      // For special case, when changing authentication from None to CHAP, and click APPLY button directly.
      // It helps to display error and disable APPLY button.
      this.iscsiSettings.get("authSpec").updateValueAndValidity();
      if (this.iscsiSettings.invalid) {
         // Make sure any errors from policy preselection are displayed
         this.iscsiSettingsForm.markAsTouched();
         return;
      }

      let editSpec: VsanIscsiConfigEditSpec = null;
      if (!this.isServiceEnabled) {
         // Disable iSCSI target service
         editSpec = VsanIscsiConfigEditSpec.Factory.create(false, null, null, null, null);
      } else {
         const authSpec: VsanIscsiAuthSpec = this.iscsiSettings.controls.authSpec.value
               // A temporary workaround for https://bugzilla.eng.vmware.com/show_bug.cgi?id=2750778
               || VsanIscsiAuthSpec.Factory.create(IscsiTargetAuthType.NoAuth.toString(), null, null, null, null);
         // Edit iSCSI target service config
         editSpec = VsanIscsiConfigEditSpec.Factory.create(true,
               this.iscsiSettings.controls.network.value,
               this.iscsiSettings.controls.port.value,
               authSpec,
               this.iscsiSettings.controls.policy.value);
      }

      this.editIscsiConfig(editSpec);
   };

   @Loader()
   private async editIscsiConfig(editSpec: VsanIscsiConfigEditSpec): Promise<void> {
      const taskRef: ManagedObject = await this.iscsiMutationProvider.editIscsiConfig(this.clusterRef, editSpec);
      this.handleMutationResult(taskRef);
   }

   policyValidator: ValidatorFn = (control: AbstractControl): ValidationErrors | null => {
      // The error must not prevent settings to be applied when the service is off.
      if (!this.isServiceEnabled) {
         return null;
      }
      this.policyMessage = VsanIscsiUtil.validatePolicy(control.value, this.chooseEmptyPolicyError());
      if (this.policyMessage) {
         return {policyInvalid: {value: control.value}};
      }
      return null;
   };

   private chooseEmptyPolicyError(): string {
      return !this.originalIsServiceEnabled
            ? VsanUiUtils.getString("vsan.iscsi.edit.storagePolicy.homeObject.enableWarning")
            : VsanUiUtils.getString("vsan.iscsi.edit.storagePolicy.homeObject.editWarning");
   }

   get blockApplyButton(): boolean {
      return this.loading
            || this.alertMessages && this.alertMessages.length > 0
            || this.isEmptyCluster
            || this.isServiceEnabled && this.iscsiSettings && this.iscsiSettings.touched && this.iscsiSettings.invalid;
   }

   private handleMutationResult = (taskRef: ManagedObject) => {
      this.close(taskRef);
   };

   @ErrorHandler()
   private handleError(error: string): void {
      this.alertMessages = [error];
   }

   private clearAlert = () => {
      this.alertMessages = null;
   };

   private close = (taskRef: ManagedObject = null) => {
      VsanUiUtils.closeModalDialog(taskRef);
   };
}
