import {Component, Input, OnInit} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {VsanServiceStatus} from "@generated/vsan-service-status";
import {PerfStatsObjectInfo} from "@generated/perf-stats-object-info";
import {ServiceUtils} from "./service.utils";
import {Privileges} from "@generated/privileges";
import {RefreshHandler} from "@service/global-refresh.service";
import {VsanConfigService} from "@generated/vsan-config-service";
import {TaskMonitorService} from "@service/task-monitor-service";
import {ModalBuilder} from "@util/modal-builder";
import {VsanUiUtils} from "@util/vsan-util";
import {NavigationService} from "@navigation/navigation.service";
import {Icon} from "@util/icon";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {PersistedProperty} from "@service/browser-storage.service";
import {VsanRoutes} from "../../vsan-routes";
import {VsanDialogProperties, VsanDialogUtil} from "@util/vsan-dialog-util";

@Component({
   selector: "vsan-performance-service",
   templateUrl: "performance-service.html",
})
export class PerformanceServiceComponent implements OnInit {

   ServiceUtils: typeof ServiceUtils = ServiceUtils;
   Icon: typeof Icon = Icon;

   @Input()
   clusterRef: ManagedObject;

   @Input()
   clusterName: string;

   @Input()
   vcCapabilityData: VsanCapabilityData;

   @PersistedProperty("vsan-perf-service-card-expanded-state")
   isExpanded: boolean;

   @Input()
   clusterPermissions: Map<string, boolean>;

   @Input()
   vcPermissions: Map<string, boolean>;

   @Input()
   status: VsanServiceStatus;

   @Input()
   config: PerfStatsObjectInfo;

   editClusterPermission: boolean;
   readPoliciesPermission: boolean;

   errorMessage: string;

   constructor(private configService: VsanConfigService,
         private taskService: TaskMonitorService,
         private navigationService: NavigationService) {
   }

   ngOnInit(): void {
      this.editClusterPermission = this.clusterPermissions.get(Privileges.EDIT_CLUSTER());
      this.readPoliciesPermission = this.vcPermissions.get(Privileges.READ_POLICIES());
      // Expand the card by default if the service is enabled
      if (this.status === VsanServiceStatus.ENABLED && VsanUiUtils.isUnset(this.isExpanded)) {
         this.isExpanded = true;
      }
   }

   @RefreshHandler
   clearErrors(): void {
      this.errorMessage = null;
   }

   get editTooltip(): string {
      if (!this.config) {
         // The config data is still loading
         return "";
      }
      if (!this.editClusterPermission || !this.readPoliciesPermission) {
         return VsanUiUtils.getString("vsan.common.nopermissions");
      }
      return "";
   }

   edit(): void {
      const size: VsanDialogProperties = this.config.serviceEnabled
            ? VsanDialogUtil.PERF_SVC_EDIT_ACTION_DIALOG
            : VsanDialogUtil.PERF_SVC_ENABLE_ACTION_DIALOG;
      new ModalBuilder(VsanRoutes.PERFORMANCE_CONFIG)
            .setSize(size.width, size.height)
            .open({
               clusterRef: this.clusterRef,
               clusterName: this.clusterName,
            })
            .then(this.awaitThenInvoke);
   }

   onPolicyClicked(storagePolicyId: string): void {
      this.navigationService.navigateToVmStoragePoliciesView(storagePolicyId);
   }

   private awaitThenInvoke = (taskRef: ManagedObject) => {
      this.taskService.getAwaitResult(taskRef)
            .then(() => VsanUiUtils.globalRefresh())
            .catch(e => this.errorMessage = e);
   };
}
