import {ChangeDetectionStrategy, Component, EventEmitter, Input, Output} from "@angular/core";
import {VsanConfigSpec} from "@generated/vsan-config-spec";
import {ModalBuilder} from "@util/modal-builder";
import {ManagedObject} from "@service/managed-object";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {ServiceUtils} from "./service.utils";
import {PersistedProperty} from "@service/browser-storage.service";
import {VsanUiUtils} from "@util/vsan-util";
import {EditNetworkContext} from "@navigation/context/edit-network-context";

@Component({
   selector: "vsan-network-options",
   templateUrl: "network-options.html",
   styleUrls: ["network-options.scss"],
   changeDetection: ChangeDetectionStrategy.OnPush,
})
export class NetworkOptionsComponent {

   @Input()
   vsanConfig: VsanConfigSpec;

   @Input()
   clusterRef: ManagedObject;

   @Input()
   clusterName: string;

   @Input()
   clusterCapabilityData: VsanCapabilityData;

   @Input()
   editClusterPermission: boolean;

   @Input()
   isStretchedCluster: boolean;

   @Input()
   isEmptyCluster: boolean;

   @Output()
   editTaskStarted: EventEmitter<ManagedObject> = new EventEmitter<ManagedObject>();

   @PersistedProperty("vsan-network-settings-card-expanded-state")
   isExpanded: boolean;

   static readonly RDMA_DIALOG_HEIGHT_UNSUPPORTED_HOSTS: number = 490;
   private static readonly RDMA_DIALOG_HEIGHT: number = 300;
   private static readonly RDMA_DIALOG_WIDTH: number = 670;

   get rdmaStatusLabel(): string {
      return ServiceUtils.geStatusLabel(this.vsanConfig.enableRdma, this.clusterCapabilityData.isRdmaSupported);
   }

   get unavailableMessage(): string {
      if (!this.editClusterPermission) {
         return VsanUiUtils.getString("vsan.common.nopermissions");
      } else if (this.clusterCapabilityData && !this.clusterCapabilityData.isRdmaSupported) {
         return VsanUiUtils.getString("vsan.services.network.rdmaSupport.notSupported");
      } else if (this.isEmptyCluster) {
         return VsanUiUtils.getString("vsan.services.network.rdmaSupport.emptyCluster");
      } else if (this.vsanConfig.advancedOptions.largeClusterSupportEnabled && !this.vsanConfig.enableRdma) {
         return VsanUiUtils.getString("vsan.services.network.rdmaSupport.notSupportedLargeScaleCluster");
      } else if (this.isStretchedCluster && !this.clusterCapabilityData.isRdmaStretchedClusterSupported
            && !this.vsanConfig.enableRdma ) {
         return VsanUiUtils.getString("vsan.services.network.rdmaSupport.notSupportedStretchedCluster");
      }

      return "";
   }

   edit(): void {
      const context: EditNetworkContext = {
         clusterRef: this.clusterRef,
         clusterName: this.clusterName,
         isStretchedClusterUnsupported: this.isStretchedCluster && !this.clusterCapabilityData.isRdmaStretchedClusterSupported,
         isLargeClusterSupportEnabled: this.vsanConfig.advancedOptions.largeClusterSupportEnabled,
      };

      new ModalBuilder("services/editNetwork")
            .setSize(NetworkOptionsComponent.RDMA_DIALOG_WIDTH, NetworkOptionsComponent.RDMA_DIALOG_HEIGHT)
            .open(context)
            .then((taskRef: ManagedObject) => {
               this.editTaskStarted.emit(taskRef);
            });
   }
}
