import {ChangeDetectionStrategy, Component, EventEmitter, Input, Output} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {Privileges} from "@generated/privileges";
import {VsanCapabilityData} from "@generated/vsan-capability-data";
import {AdvancedOptionsInfo} from "@generated/advanced-options-info";
import {ManagedObject} from "@service/managed-object";
import {ModalBuilder} from "@util/modal-builder";
import {ServiceUtils} from "./service.utils";
import {PersistedProperty} from "@service/browser-storage.service";

@Component({
   selector: "vsan-advanced-options",
   templateUrl: "advanced-options.html",
   changeDetection: ChangeDetectionStrategy.OnPush,
})
export class AdvancedOptionsComponent {
   ServiceUtils = ServiceUtils;

   @Input()
   config: AdvancedOptionsInfo;

   @Input()
   clusterRef: ManagedObject;

   @Input()
   clusterName: string;

   @Input()
   vcCapabilityData: VsanCapabilityData;

   @Input()
   clusterCapabilityData: VsanCapabilityData;

   @Input()
   permissions: Map<string, boolean>;

   @Output()
   editTaskStarted: EventEmitter<ManagedObject> = new EventEmitter<ManagedObject>();

   @PersistedProperty("vsan-advanced-options-card-expanded-state")
   isExpanded: boolean;

   private editClusterPermission: boolean;

   ngOnInit() {
      this.editClusterPermission = this.permissions.get(Privileges.EDIT_CLUSTER());
      // If the user navigates to this page from the Skyline Health page, open the Edit Advanced Options dialog.
      // TODO dtsvetkova: confirm if this is actually needed.
      // The health page can just open the edit dialog without navigating here.
      const context: any = VsanUiUtils.getNavigationData();
      if (context && context.advancedOptionsExpanded) {
         this.isExpanded = context.advancedOptionsExpanded;
         this.edit();
      }
   }

   get editDisabledTitle(): string {
      if (!this.editClusterPermission) {
         return VsanUiUtils.getString("vsan.common.nopermissions");
      }
      return "";
   }

   edit() {
      new ModalBuilder("services/editAdvancedOptions")
            .setSize(800, 500)
            .open({
               clusterRef: this.clusterRef,
               clusterName: this.clusterName,
               vcCapabilityData: this.vcCapabilityData,
               clusterCapabilityData: this.clusterCapabilityData,
            })
            .then(taskRef => this.editTaskStarted.emit(taskRef));
   }
}